import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from './store'
import { actionCreators as loginActionCreators } from '../../pages/login/store'
import { Link, withRouter } from 'react-router-dom'

import {
    HeaderWrapper, LogoWrapper, Logo, LogoTitle, UserMessage
} from './style';

import { Menu } from 'antd';



class Header extends React.Component {

    constructor(props) {
        super(props)
        this.props.handleHeaderKey(this.props.history.location.pathname)

        window.storage.load({
            key: 'userName',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            // console.log(ret.name)
            this.props.setUserName(ret.name)

        }).catch(err => {
            console.log(err)
        });

        window.storage.load({
            key: 'loginStatus',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setLoginStatus(ret.isLogin)

        }).catch(err => {
            console.log(err)
        });

    }

    render() {
        if (this.props.userName.indexOf("teacher") != -1) {
            return <HeaderWrapper>
                <Link to="/">
                    <LogoWrapper>
                        <Logo></Logo>
                        <LogoTitle>简道 智能排课系统</LogoTitle>
                    </LogoWrapper>
                </Link>
                <Menu
                    mode="horizontal"
                    theme="light"
                    defaultSelectedKeys={["/instructor"]}
                    selectedKeys={[this.props.headerKey]}
                    style={{ lineHeight: '64px' }}>
                    <Menu.Item
                        style={{ marginRight: '30px' }}
                        key="/instructor"><Link to="/instructor">首页</Link></Menu.Item>
                    <Menu.Item
                        style={{ marginRight: '30px' }}
                        key="/home"><Link to="/home/tongzhi">我的排课</Link></Menu.Item>
                    <Menu.Item key="/help"><Link to="/help">帮助文档</Link></Menu.Item>

                    <UserMessage>
                        <span className='userName'>{this.props.userName}</span>
                        <Link to="/">
                            <span onClick={this.props.logout}>退出</span>
                        </Link>
                    </UserMessage>
                </Menu>
            </HeaderWrapper>
        } else {
            return <HeaderWrapper>
                <Link to="/">
                    <LogoWrapper>
                        <Logo></Logo>
                        <LogoTitle>简道 智能排课系统</LogoTitle>
                    </LogoWrapper>
                </Link>
                <Menu
                    mode="horizontal"
                    theme="light"
                    defaultSelectedKeys={["/instructor"]}
                    selectedKeys={[this.props.headerKey]}
                    style={{ lineHeight: '64px' }}>
                    <Menu.Item
                        style={{ marginRight: '30px' }}
                        key="/instructor"><Link to="/instructor">首页</Link></Menu.Item>
                    <Menu.Item
                        style={{ marginRight: '30px' }}
                        key="/home"><Link to="/home/jieci">我的排课</Link></Menu.Item>
                    <Menu.Item key="/help"><Link to="/help">帮助文档</Link></Menu.Item>

                    <UserMessage>
                        <span className='userName'>{this.props.userName}</span>
                        <Link to="/">
                            <span onClick={this.props.logout}>退出</span>
                        </Link>
                    </UserMessage>
                </Menu>
            </HeaderWrapper>
        }

    }

    componentWillUpdate() {
        this.props.handleHeaderKey(this.props.history.location.pathname)
    }

}



const mapStateToProps = (state) => {
    return {
        userName: state.get('header').get('userName'),
        headerKey: state.get('header').get('headerKey')
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleHeaderKey(history) {
            if (history.indexOf("home") > -1) {
                const str = '/home'
                dispatch(actionCreators.handleHeaderKeyAction(str))
            } else {
                dispatch(actionCreators.handleHeaderKeyAction(history))
            }
        },

        setUserName(userName) {
            if (userName !== '') {
                dispatch(actionCreators.handleSetUserNameAction(userName))
            }
        },

        logout() {
            dispatch(loginActionCreators.setLoginStatusAction(false))
        },

        setLoginStatus(isLogin) {
            dispatch(loginActionCreators.setLoginStatusAction(isLogin))
        }
    }
}


const HeaderContainer = connect(mapStateToProps, mapDispatchToProps)(Header)
export default withRouter(HeaderContainer)
