import React from 'react';
import { HashRouter as Router, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import './RNLocalStorage'
import store from './store'

import Home from './pages/home'
import Login from './pages/login'
import Signup from './pages/signup'
import Help from './pages/help'
import Instructor from './pages/instructor'
import Header from './commen/header'
import Judge from './pages/judge'

import Jieci from './pages/home/components/jieci';
import Jiaoshi from './pages/home/components/jiaoshi'
import Xuesheng from './pages/home/components/xuesheng'
import Tiqian from './pages/home/components/tiqian'
import Shijian from './pages/home/components/shijian'
import Paike from './pages/home/components/paike'
import Chakan from './pages/home/components/chakan'

import Tongzhi from './pages/home/components/tongzhi'

import { Layout } from 'antd'

import createHistory from 'history/createHashHistory'
const history = createHistory()


class App extends React.Component {
  render() {
    return (
      <Provider store={store}>
        <Router history={history}>
          <Route render={({ location }) => {
            return (
              <div>
                <Route exact path='/' component={Judge}></Route>
                <Layout><Header /></Layout>

                <Route path='/home' render={() => <Home>
                  <Route path='/home/jieci' component={Jieci}></Route>
                  <Route path='/home/xuesheng' component={Xuesheng}></Route>
                  <Route path='/home/jiaoshi' component={Jiaoshi}></Route>
                  <Route path='/home/tiqian' component={Tiqian}></Route>
                  <Route path='/home/shijian' component={Shijian}></Route>
                  <Route path='/home/paike' component={Paike}></Route>
                  <Route path='/home/chakan' component={Chakan}></Route>
                  <Route path='/home/tongzhi' component={Tongzhi}></Route>
                </Home>}></Route>
                <Route path='/instructor' exact component={Instructor}></Route>
                <Route path='/help' exact component={Help}></Route>

                <Route path='/login' exact component={Login}></Route>
                <Route path='/signup' exact component={Signup}></Route>
              </div>
            )
          }} />
        </Router>
      </Provider>
    )
  }
}

export default App;