import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import {
    TitleLine, TitleText, NextStep, PreStep, StepLine, HomeMainWrapper, PreDefault, TipsHint, TongzhiDefault, TongzhiActive
} from '../style';
import { Button, Divider, Table, message } from 'antd'
import { Link } from 'react-router-dom'






class Tongzhi extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'canTeacherPaike',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setXietong(ret.canTeacherPaike)
        }).catch(err => {
            console.log(err)
        });
    }

    render() {
        if (this.props.canTeacherPaike) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>通知动态</TitleText>
                    <Link to="/home/shijian"><TongzhiActive /></Link>
                    <Link to="/instructor"><PreStep className="tongzhi">返回首页</PreStep></Link>
                    <StepLine className="jieci" />
                    <Link to="/home/shijian"><NextStep className="jieci">协同排课</NextStep></Link>
                </HomeMainWrapper>
            </div>
        } else {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>通知动态</TitleText>
                    <TongzhiDefault />
                    <Link to="/instructor"><PreStep className="tongzhi">返回首页</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        }

    }
}





const mapStateToProps = (state) => {
    return {
        canTeacherPaike: state.getIn(['home', 'canTeacherPaike']),
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setXietong(value) {
            dispatch(actionCreators.handleXietongAction(value))
        }
    }
}



export default connect(mapStateToProps, mapDispatchToProps)(Tongzhi)
