import * as constants from './constants'
import { fromJS } from 'immutable'


const defaultState = fromJS({
    userName: 'contact@chucktzh.com',
    headerKey: ''
})

export default (state = defaultState, action) => {

    switch (action.type) {
        case constants.HANDLE_KEY:
            return state.set('headerKey', action.history)
        case constants.CHANGE_LOGIN:
            if (action.account != null) {
                return state.set('userName', action.account)
            }
        default:
            return state
    }

}