import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import {
    TopHint, TitleLine, TitleText, HomeTopWrapper, HomeButtomWrapper, SearchDefault, NextStep, PreStep, StepLine, HomeMainWrapper, FenbanWrapper, FenbanInput, PreDefault, TipsHint, TeacherChakanDefault
} from '../style';
import { Button, Divider, Table, message } from 'antd'
import { Link } from 'react-router-dom'
import exportToExcel from '../../../exportToExcel'






class Chakan extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'paikeState',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            if (ret.paikeState === 3) {
                this.props.handlePaikeStart(ret.paikeState)
            }
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'shijianColumn',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setShijianColumn(ret.shijianColumn)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'randomSchedule',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setRandomSchedule(ret.randomSchedule)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciDay',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciDay(ret.day)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciCourse',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciCourse(ret.course)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'randomScheduleId',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setId(ret.randomScheduleId)
        }).catch(err => {
            console.log(err)
        });

    }

    render() {
        if (this.props.paikeState === 3 && this.props.userName.indexOf("teacher") === -1) {
            const columns = this.props.shijianColumn.map((col) => {
                return {
                    ...col,
                    align: "center",
                    render: (text, record, index) => {
                        if (text.indexOf("第") !== -1) {
                            return <span style={{ fontWeight: 450, color: 'rgba(0, 0, 0, 0.85)' }}>{text}</span>
                        } else {
                            return <span>{text[0]}{text[1]}<br></br>{text.substring(2)}</span>
                        }
                    },
                }
            })
            return <div>
                <HomeTopWrapper>
                    <TitleLine />
                    <TitleText>查看课表 - 快速查询</TitleText>
                    <FenbanWrapper>
                        <FenbanInput placeholder='请输入学号或工号查询课表' innerRef={(input) => { this.idValue = input }} />
                        <Divider type="vertical" className="divider" />
                        <span className="selected" style={{ fontSize: 16 }} onClick={() => this.props.handleRandomSchedule(this.props.day, this.props.course, this.idValue)}>查询</span>
                    </FenbanWrapper>
                    <a id="exportToExcel" href=""></a>
                    <Button type="default" className="templeteButton" onClick={() => { exportToExcel(columns, this.props.randomSchedule, "schedule") }}>打包下载</Button>
                    <Button type="primary" className="uploadButton" onClick={() => { exportToExcel(columns, this.props.randomSchedule, "schedule") }}>下载当前课表</Button>
                </HomeTopWrapper>
                {(() => {
                    if (this.props.randomSchedule.length !== 0) {
                        return <HomeButtomWrapper className="hasItem">
                            <Table
                                ref='tables'
                                id="table-to-xls"
                                rowKey={(r, i) => (i)}
                                className="timeTable"
                                dataSource={this.props.randomSchedule}
                                columns={columns}
                                bordered={true}
                                pagination={{
                                    simple: true,
                                    position: 'top',
                                    showQuickJumper: true,
                                    hideOnSinglePage: true,
                                    total: this.props.randomSchedule.length,
                                    hideOnSinglePage: false,
                                    defaultPageSize: 6,
                                }}
                            >
                            </Table>
                            <TipsHint className="tips">当前课表编号: {this.props.randomScheduleId}</TipsHint>
                            <Link to="/home/paike"><PreStep className="xuesheng hasItemStep">上一步</PreStep></Link>
                            <StepLine className="xuesheng hasItemLine" />
                            <Link to="/home/instructor"><NextStep className="xuesheng hasItemStep">返回首页</NextStep></Link>
                        </HomeButtomWrapper>
                    } else {
                        return <div>
                            <HomeButtomWrapper>
                                <SearchDefault />
                                <Link to="/home/paike"><PreStep className="xuesheng">上一步</PreStep></Link>
                                <StepLine className="xuesheng" />
                                <Link to="/instructor"><NextStep className="xuesheng">返回首页</NextStep></Link>
                            </HomeButtomWrapper>
                        </div>
                    }
                })()}
            </div>
        } else if (this.props.paikeState === 3 && this.props.userName.indexOf("teacher") !== -1) {
            // this.props.handleRandomTeacherTimetable(this.props.day, this.props.course)
            const columns = this.props.shijianColumn.map((col) => {
                return {
                    ...col,
                    align: "center",
                    render: (text, record, index) => {
                        if (text.indexOf("第") !== -1) {
                            return <span style={{ fontWeight: 450, color: 'rgba(0, 0, 0, 0.85)' }}>{text}</span>
                        } else {
                            return <span>{text}</span>
                        }
                    },
                }
            })
            let tempRandomData = this.randomTeacherTimetable(this.props.day, this.props.course)
            let tempId = this.randomId()
            return <div>
                <HomeTopWrapper>
                    <TitleLine />
                    <TitleText>查看课表</TitleText>
                    <TopHint>您的课表编号是: {tempId} 。 如有疑问，请联系教务员处理</TopHint>
                    <a id="exportToExcel" href=""></a>
                    <Button type="primary" className="uploadButton" onClick={() => { exportToExcel(columns, tempRandomData, "schedule") }}>下载课表</Button>
                </HomeTopWrapper>
                {(() => {
                    if (tempRandomData.length !== 0) {
                        return <HomeButtomWrapper className="hasItem">
                            <Table
                                ref='tables'
                                id="table-to-xls"
                                rowKey={(r, i) => (i)}
                                className="timeTable"
                                dataSource={tempRandomData}
                                columns={columns}
                                bordered={true}
                                pagination={{
                                    simple: true,
                                    position: 'top',
                                    showQuickJumper: true,
                                    hideOnSinglePage: true,
                                    total: tempRandomData.length,
                                    hideOnSinglePage: false,
                                    defaultPageSize: 10,
                                }}
                            >
                            </Table>
                            <Link to="/home/tiqian"><PreStep className="xuesheng tongzhibuttom">返回首页</PreStep></Link>
                            <StepLine className="xuesheng hasItemLine" />
                        </HomeButtomWrapper>
                    }
                })()}
            </div>
        } else if (this.props.userName.indexOf("teacher") !== -1 && this.props.paikeState !== 3) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>查看课表</TitleText>
                    <TeacherChakanDefault />
                    <Link to="/instructor"><PreStep className="tongzhi">返回首页</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else if (this.props.userName.indexOf("teacher") === -1 && this.props.paikeState !== 3) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>查看课表 - 快速查询</TitleText>
                    <Link to="/home/paike"><PreDefault /></Link>
                    <Link to="/home/paike"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                    <Link to="/instructor"><NextStep className="jieci">返回首页</NextStep></Link>
                </HomeMainWrapper>
            </div>

        }

    }

    randomTeacherTimetable = (day, course) => {
        const classroomArray = ["0号教室", "1号教室", "2号教室", "3号教室", "4号教室", "5号教室", "6号教室", "7号教室", "8号教室", "9号教室"]
        let tempData = []
        if (day !== "" && course !== "") {
            for (var i = 0; i < parseInt(course); i++) {
                let tempRowJson = {}
                for (var j = 0; j <= parseInt(day); j++) {
                    if (j === 0) {
                        tempRowJson['jieshu'] = "第" + (i + 1) + "节"
                    }
                    if (j === 1) {
                        let subNum = Math.floor(Math.random() * 10)
                        let classroomNum = Math.floor(Math.random() * 10)
                        let scheduleCell = classroomArray[classroomNum]
                        if (subNum <= 2) {
                            tempRowJson['day1'] = scheduleCell
                        } else {
                            tempRowJson['day1'] = " "
                        }

                    }
                    if (j === 2) {
                        let subNum = Math.floor(Math.random() * 10)
                        let classroomNum = Math.floor(Math.random() * 10)
                        let scheduleCell = classroomArray[classroomNum]
                        if (subNum <= 2) {
                            tempRowJson['day2'] = scheduleCell
                        } else {
                            tempRowJson['day2'] = " "
                        }
                    }
                    if (j === 3) {
                        let subNum = Math.floor(Math.random() * 10)
                        let classroomNum = Math.floor(Math.random() * 10)
                        let scheduleCell = classroomArray[classroomNum]
                        if (subNum <= 2) {
                            tempRowJson['day3'] = scheduleCell
                        } else {
                            tempRowJson['day3'] = " "
                        }
                    }
                    if (j === 4) {
                        let subNum = Math.floor(Math.random() * 10)
                        let classroomNum = Math.floor(Math.random() * 10)
                        let scheduleCell = classroomArray[classroomNum]
                        if (subNum <= 3) {
                            tempRowJson['day4'] = scheduleCell
                        } else {
                            tempRowJson['day4'] = " "
                        }
                    }
                    if (j === 5) {
                        let subNum = Math.floor(Math.random() * 10)
                        let classroomNum = Math.floor(Math.random() * 10)
                        let scheduleCell = classroomArray[classroomNum]
                        if (subNum <= 3) {
                            tempRowJson['day5'] = scheduleCell
                        } else {
                            tempRowJson['day5'] = " "
                        }
                    }
                    if (j === 6) {
                        let subNum = Math.floor(Math.random() * 10)
                        let classroomNum = Math.floor(Math.random() * 10)
                        let scheduleCell = classroomArray[classroomNum]
                        if (subNum <= 3) {
                            tempRowJson['day6'] = scheduleCell
                        } else {
                            tempRowJson['day6'] = " "
                        }
                    }
                    if (j === 7) {
                        let subNum = Math.floor(Math.random() * 10)
                        let classroomNum = Math.floor(Math.random() * 10)
                        let scheduleCell = classroomArray[classroomNum]
                        if (subNum <= 3) {
                            tempRowJson['day7'] = scheduleCell
                        } else {
                            tempRowJson['day7'] = " "
                        }
                    }
                }
                let tempArray = [tempRowJson]
                tempData = tempData.concat(tempArray)
            }
        }
        return tempData
    }

    randomId() {
        let id = "";
        for (var i = 0; i < 6; i++) {
            id += Math.floor(Math.random() * 10);
        }
        return id
    }
}





const mapStateToProps = (state) => {
    return {
        paikeState: state.getIn(['home', 'paikeState']),
        randomSchedule: state.getIn(['home', 'randomSchedule']).toJS(),
        randomScheduleId: state.getIn(['home', 'randomScheduleId']),

        //表格标题（第一行）
        shijianColumn: state.getIn(['home', 'shijianColumn']).toJS(),

        day: state.getIn(['home', 'day']),
        course: state.getIn(['home', 'course']),

        userName: state.get('header').get('userName'),

        randomTeacherTimetable: state.getIn(['home', 'randomSchedule']).toJS(),
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handlePaikeStart(paikeState) {
            dispatch(actionCreators.handlePaikeStartAction(paikeState))
        },
        setShijianColumn(value) {
            dispatch(actionCreators.setShijianColumnAction(value))
        },
        handleRandomSchedule(day, course, id) {
            if (id.value !== "") {
                dispatch(actionCreators.handleRandomScheduleAction(day, course))
                dispatch(actionCreators.handleScheduleIdAction(id.value))
            } else {
                message.warning('请先输入一个学生或教师ID')
            }

        },
        setId(id) {
            dispatch(actionCreators.handleScheduleIdAction(id))
        },
        setRandomSchedule(value) {
            dispatch(actionCreators.setRandomScheduleAction(value))
        },
        setJieciDay(value) {
            dispatch(actionCreators.handleDayChangeAction(value, true, "", ""))
        },
        setJieciCourse(value) {
            dispatch(actionCreators.handleCourseChangeAction(value, true, "", ""))
        },
        handleRandomTeacherTimetable(day, course) {
            dispatch(actionCreators.handleRandomTeacherTimetableAction(day, course))
            let id = "";
            for (var i = 0; i < 6; i++) {
                id += Math.floor(Math.random() * 10);
            }
            dispatch(actionCreators.handleScheduleIdAction(id))
        }
    }
}



export default connect(mapStateToProps, mapDispatchToProps)(Chakan)
