import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import 'immutable'
import * as XLSX from 'xlsx';
import {
    TitleLine, TitleText, HomeTopWrapper, HomeButtomWrapper, TopHint, TeacherDefault, NextStep, PreStep, StepLine, TotalData
} from '../style';
import { Button, Table } from 'antd'
import { Link } from 'react-router-dom'

class Jiaoshi extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'teacherData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setTeacherData(ret.teacherData)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciDay',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciDay(ret.day)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciCourse',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciCourse(ret.course)
        }).catch(err => {
            console.log(err)
        });
    }

    render() {
        if (this.props.teacherData.length !== 0) {
            let renderData = []
            if (this.props.teacherData.length % 2 === 0) {
                for (var i = 0; i < this.props.teacherData.length; i = i + 2) {
                    let tempArray = [{
                        id1: this.props.teacherData[i]['id1'],
                        name1: this.props.teacherData[i]['name1'],
                        subject1: this.props.teacherData[i]['subject1'],
                        id2: this.props.teacherData[i + 1]['id1'],
                        name2: this.props.teacherData[i + 1]['name1'],
                        subject2: this.props.teacherData[i + 1]['subject1'],
                    }]
                    renderData = renderData.concat(tempArray)
                }
            } else {
                var j = 0
                for (j; j < this.props.teacherData.length; j = j + 2) {
                    if ((j + 1) >= (this.props.teacherData.length - 1)) {
                        let tempArray = [{
                            id1: this.props.teacherData[j]['id1'],
                            name1: this.props.teacherData[j]['name1'],
                            subject1: this.props.teacherData[j]['subject1'],
                        }]
                        renderData = renderData.concat(tempArray)
                    } else {
                        let tempArray = [{
                            id1: this.props.teacherData[j]['id1'],
                            name1: this.props.teacherData[j]['name1'],
                            subject1: this.props.teacherData[j]['subject1'],
                            id2: this.props.teacherData[j + 1]['id1'],
                            name2: this.props.teacherData[j + 1]['name1'],
                            subject2: this.props.teacherData[j + 1]['subject1'],
                        }]
                        renderData = renderData.concat(tempArray)
                    }
                }
            }
            return <div>
                <HomeTopWrapper>
                    <TitleLine />
                    <TitleText>基础信息 - 教师信息</TitleText>
                    <TopHint>Tips：对表格填写有疑问？<Link to="/help" onClick={this.props.handleAnchor}><span className="link">点击查看帮助文档</span></Link></TopHint>
                    <Button type="default" className="templeteButton"><a className="download" href="/api/Teacher.xlsx" download="Teacher.xlsx">模板下载</a></Button>
                    <input ref="inputButton" type='file' accept='.xlsx, .xls' className="inputButton" onChange={(e) => { this.props.handleTeacherFileUpload(e, this.props.day, this.props.course) }} />
                    <Button type="primary" className="uploadButton" onClick={this.handleButton.bind(this)}>重新上传</Button>
                </HomeTopWrapper>
                <HomeButtomWrapper className="hasItem">
                    <Table
                        rowKey="id1"
                        className="table"
                        dataSource={renderData}
                        columns={this.props.teacherColumn}
                        bordered={true}
                        pagination={{
                            simple: true,
                            position: 'top',
                            showQuickJumper: true,
                            hideOnSinglePage: true,
                            total: renderData.length,
                            hideOnSinglePage: false
                        }}
                    >
                    </Table>
                    <TotalData>教师信息（共{this.props.teacherData.length}条）</TotalData>
                    <Link to="/home/xuesheng"><PreStep className="hasItemStep">上一步</PreStep></Link>
                    <StepLine className="hasItemLine" />
                    <Link to="/home/tiqian"><NextStep className="hasItemStep">下一步</NextStep></Link>
                </HomeButtomWrapper>
            </div>
        } else {
            return <div>
                <HomeTopWrapper>
                    <TitleLine />
                    <TitleText>基础信息 - 教师信息</TitleText>
                    <TopHint>Tips：对表格填写有疑问？<Link to="/help" onClick={this.props.handleAnchor}><span className="link">点击查看帮助文档</span></Link></TopHint>
                    <Button type="default" className="templeteButton"><a className="download" href="/api/Teacher.xlsx" download="Teacher.xlsx">模板下载</a></Button>
                    <input ref="inputButton" type='file' accept='.xlsx, .xls' className="inputButton" onChange={(e) => { this.props.handleTeacherFileUpload(e, this.props.day, this.props.course) }} />
                    <Button type="primary" className="uploadButton" onClick={this.handleButton.bind(this)}>上传信息</Button>
                </HomeTopWrapper>
                <HomeButtomWrapper>
                    <TeacherDefault />
                    <Link to="/home/xuesheng"><PreStep className="xuesheng">上一步</PreStep></Link>
                    <StepLine className="xuesheng" />
                    <Link to="/home/tiqian"><NextStep className="xuesheng">下一步</NextStep></Link>
                </HomeButtomWrapper>
            </div>
        }


    }

    handleButton() {
        this.refs.inputButton.click();
    }
}


const mapStateToProps = (state) => {
    return {
        teacherData: state.getIn(['home', 'teacherData']).toJS(),
        teacherColumn: state.getIn(['home', 'teacherColumn']).toJS(),
        day: state.getIn(['home', 'day']),
        course: state.getIn(['home', 'course']),
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleTeacherFileUpload(e, day, course) {
            var rABS = true;
            var files = e.target.files, f = files[0]
            var reader = new FileReader()
            reader.onload = function (e) {
                var data = e.target.result
                if (!rABS) data = new Uint8Array(data);
                var workbook = XLSX.read(data, { type: rABS ? 'binary' : 'array' })

                /* DO SOMETHING WITH workbook HERE */
                let first_worksheet = workbook.Sheets[workbook.SheetNames[0]];
                let jsonArr = XLSX.utils.sheet_to_json(first_worksheet, { header: 1 });
                let tempData = []
                for (var i = 0; i < jsonArr.length; i++) {
                    if (typeof (jsonArr[i][0]) !== "undefined" && typeof (jsonArr[i][1]) !== "undefined" && typeof (jsonArr[i][2]) !== "undefined") {
                        let tempArray = [{
                            id1: jsonArr[i][0],
                            name1: jsonArr[i][1],
                            subject1: jsonArr[i][2],
                        }]
                        tempData = tempData.concat(tempArray)
                    }
                }
                dispatch(actionCreators.handleTeacherFileUploadAction(tempData, false, day, course))
            }
            if (rABS) reader.readAsBinaryString(f); else reader.readAsArrayBuffer(f)
        },
        setTeacherData(data) {
            dispatch(actionCreators.handleTeacherFileUploadAction(data, true))
        },
        setJieciDay(value) {
            dispatch(actionCreators.handleDayChangeAction(value, true))
        },
        setJieciCourse(value) {
            dispatch(actionCreators.handleCourseChangeAction(value, true))
        },
        handleAnchor() {
            let type = 2;
            dispatch(actionCreators.handleAnchorAction(type))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Jiaoshi)
