import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import {
    HomeMainWrapper, TitleLine, TitleText, NextStep, PreStep, StepLine, ShijianTeacherDefault, ShijianBasicDefault, PaikeBothDefault, PaikeStudentDefault, PaikeDefault, OnpaikeHint, PaikeDoneDefault
} from '../style';
import { Button, Progress } from 'antd'
import { Link } from 'react-router-dom'






class Paike extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'jieciDay',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciDay(ret.day)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciCourse',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciCourse(ret.course)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciClassroom',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciClassroom(ret.classroom)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciCapacity',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciCapacity(ret.capacity)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciClass',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciClass(ret.class)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciClassnumber',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciClassnumber(ret.classnumber)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'teacherData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setTeacherData(ret.teacherData)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'studentData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setStudentData(ret.studentData)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'paikeState',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            if (ret.paikeState !== 2) {
                this.props.handlePaikeStart(ret.paikeState)
            }
        }).catch(err => {
            console.log(err)
        });
        if (this.props.paikeState === 2) {
            this.handlePaikeStart()
        }
    }

    render() {
        if ((this.props.teacherData.length === 0 | this.props.studentData.length === 0) && (this.props.day === "" | this.props.course === "" | this.props.classroom === "" | this.props.class === "" | this.props.capacity === "" | this.props.classnumber === "")) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>自动排课</TitleText>
                    <Link to="/home/jieci"><PaikeBothDefault /></Link>
                    <Link to="/home/shijian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else if (this.props.studentData.length === 0) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>自动排课</TitleText>
                    <Link to="/home/xuesheng"><PaikeStudentDefault /></Link>
                    <Link to="/home/shijian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else if (this.props.teacherData.length === 0) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>自动排课</TitleText>
                    <Link to="/home/jiaoshi"><ShijianTeacherDefault /></Link>
                    <Link to="/home/shijian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else if (this.props.day === "" | this.props.course === "" | this.props.classroom === "" | this.props.class === "" | this.props.capacity === "" | this.props.classnumber === "") {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>自动排课</TitleText>
                    <Link to="/home/jieci"><ShijianBasicDefault /></Link>
                    <Link to="/home/shijian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>自动排课</TitleText>
                    <Link to="/home/shijian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                    {(() => {
                        if (this.props.paikeState === 1) {
                            return <div>
                                <PaikeDefault />
                                <Button type="primary" className="defaultPaike" onClick={() => { this.handlePaikeStart(2) }}>开始排课</Button>
                            </div>
                        } else if (this.props.paikeState === 2) {
                            return <div>
                                <Progress
                                    className="myProgress"
                                    type="circle"
                                    strokeColor="rgba(132,104,245,1)"
                                    width={120} strokeWidth={2}
                                    percent={this.props.currentPercent}
                                    format={(percent) => {
                                        if (this.props.currentPercent < 100) {
                                            return percent.toFixed(2) + "%"
                                        } else {
                                            return <div className="paikeDone">排课完成</div>
                                        }
                                    }} />
                                <OnpaikeHint>自动排课过程中暂时不可以进行其他操作哦～</OnpaikeHint>
                                <Button type="primary" className="onPaike">正在排课...</Button>
                            </div>
                        } else if (this.props.paikeState === 3) {
                            return <div>
                                <PaikeDoneDefault />
                                <OnpaikeHint>调整规则后可以重新排课哦～</OnpaikeHint>
                                <Button type="default" className="paikeButton2" onClick={() => { this.handlePaikeStart(2) }}>重新排课</Button>
                                <Link to="/home/chakan" key="chakan">
                                    <Button type="primary" className="paikeButton">查看课表</Button>
                                </Link>
                                <Link to="/home/chakan"><NextStep className="jieci">查看课表</NextStep></Link>
                            </div>
                        }
                    })()}
                </HomeMainWrapper>
            </div>
        }
    }

    handlePaikeStart() {
        this.props.handlePaikeStart(2)
        // this.props.changePercent(0)
        this.interval = setInterval(
            () => {
                if (this.props.currentPercent < 50) {
                    let newPercent = this.props.currentPercent + 0.04
                    this.props.changePercent(newPercent)
                } else if (this.props.currentPercent < 80) {
                    let newPercent = this.props.currentPercent + 0.02
                    this.props.changePercent(newPercent)
                } else if (this.props.currentPercent < 95) {
                    let newPercent = this.props.currentPercent + 0.014
                    this.props.changePercent(newPercent)
                } else if (this.props.currentPercent < 99) {
                    let newPercent = this.props.currentPercent + 0.005
                    this.props.changePercent(newPercent)
                } else if (this.props.currentPercent < 100) {
                    let newPercent = this.props.currentPercent + 0.001
                    this.props.changePercent(newPercent)
                } else {
                    this.props.handlePaikeStart(3)
                    this.props.changePercent(0)
                    clearInterval(this.interval)
                }
            },
            1
        )
    }


    componentWillUnmount() {
        clearInterval(this.interval);
    }

}





const mapStateToProps = (state) => {
    return {
        day: state.getIn(['home', 'day']),
        course: state.getIn(['home', 'course']),
        classroom: state.getIn(['home', 'classroom']),
        capacity: state.getIn(['home', 'capacity']),
        class: state.getIn(['home', 'class']),
        classnumber: state.getIn(['home', 'classnumber']),

        studentData: state.getIn(['home', 'studentData']).toJS(),
        teacherData: state.getIn(['home', 'teacherData']).toJS(),

        paikeState: state.getIn(['home', 'paikeState']),
        currentPercent: state.getIn(['home', 'currentPercent']),
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setJieciDay(value) {
            dispatch(actionCreators.handleDayChangeAction(value, true, "", ""))
        },
        setJieciCourse(value) {
            dispatch(actionCreators.handleCourseChangeAction(value, true, "", ""))
        },
        setJieciClassroom(value) {
            dispatch(actionCreators.handleClassroomChangeAction(value))
        },
        setJieciCapacity(value) {
            dispatch(actionCreators.handleCapacityChangeAction(value))
        },
        setJieciClass(value) {
            dispatch(actionCreators.handleClassChangeAction(value))
        },
        setJieciClassnumber(value) {
            dispatch(actionCreators.handleClassnumberChangeAction(value))
        },
        setTeacherData(data) {
            dispatch(actionCreators.handleTeacherFileUploadAction(data, true))
        },
        setStudentData(data) {
            dispatch(actionCreators.handleStudentFileUploadAction(data, true))
        },
        handlePaikeStart(paikeState) {
            dispatch(actionCreators.handlePaikeStartAction(paikeState))
        },
        changePercent(percent) {
            dispatch(actionCreators.changePercentAction(percent))
        },
    }
}



export default connect(mapStateToProps, mapDispatchToProps)(Paike)
