const randomTimetable = (day, course) => {
    const subArray = ["语文", "数学", "英语", "其他", "物理", "化学", "生物", "历史", "地理", "政治"]
    const classroomArray = ["0号教室", "1号教室", "2号教室", "3号教室", "4号教室", "5号教室", "6号教室", "7号教室", "8号教室", "9号教室"]
    let tempData = []
    if (day !== "" && course !== "") {
        for (var i = 0; i < parseInt(course); i++) {
            let tempRowJson = {}
            for (var j = 0; j <= parseInt(day); j++) {
                if (j === 0) {
                    tempRowJson['jieshu'] = "第" + (i + 1) + "节"
                }
                if (j === 1) {
                    let subNum = Math.floor(Math.random() * 10)
                    let classroomNum = Math.floor(Math.random() * 10)
                    let scheduleCell = subArray[subNum] + classroomArray[classroomNum]
                    tempRowJson['day1'] = scheduleCell
                }
                if (j === 2) {
                    let subNum = Math.floor(Math.random() * 10)
                    let classroomNum = Math.floor(Math.random() * 10)
                    let scheduleCell = subArray[subNum] + classroomArray[classroomNum]
                    tempRowJson['day2'] = scheduleCell
                }
                if (j === 3) {
                    let subNum = Math.floor(Math.random() * 10)
                    let classroomNum = Math.floor(Math.random() * 10)
                    let scheduleCell = subArray[subNum] + classroomArray[classroomNum]
                    tempRowJson['day3'] = scheduleCell
                }
                if (j === 4) {
                    let subNum = Math.floor(Math.random() * 10)
                    let classroomNum = Math.floor(Math.random() * 10)
                    let scheduleCell = subArray[subNum] + classroomArray[classroomNum]
                    tempRowJson['day4'] = scheduleCell
                }
                if (j === 5) {
                    let subNum = Math.floor(Math.random() * 10)
                    let classroomNum = Math.floor(Math.random() * 10)
                    let scheduleCell = subArray[subNum] + classroomArray[classroomNum]
                    tempRowJson['day5'] = scheduleCell
                }
                if (j === 6) {
                    let subNum = Math.floor(Math.random() * 10)
                    let classroomNum = Math.floor(Math.random() * 10)
                    let scheduleCell = subArray[subNum] + classroomArray[classroomNum]
                    tempRowJson['day6'] = scheduleCell
                }
                if (j === 7) {
                    let subNum = Math.floor(Math.random() * 10)
                    let classroomNum = Math.floor(Math.random() * 10)
                    let scheduleCell = subArray[subNum] + classroomArray[classroomNum]
                    tempRowJson['day7'] = scheduleCell
                }
            }
            let tempArray = [tempRowJson]
            tempData = tempData.concat(tempArray)
        }
    }
    return tempData
}

