import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import {
    TitleLine, TitleText, HomeTopWrapper, HomeButtomWrapper, FilterWrapper, NextStep, PreStep, StepLine, HomeMainWrapper, TiqianDefault, SelectHint, FenbanWrapper, FenbanInput, DefaultSpan
} from '../style';
import { Table, Divider, Popover, message } from 'antd'
import { Link } from 'react-router-dom'

class Tiqian extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'studentData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setStudentData(ret.studentData)
        }).catch(err => {
            console.log(err)
        });

        // 如何设置 {tiqianStudentData} 的初值 ：
        // 如果 数据持久化中的数据不为空，则设置初值为缓存中的值
        // 如果 数据持久化中的数据为空，则设置初值为 studentData 处理后的值
        // 如果 studentData 发生改变，则须清空该缓存
        window.storage.load({
            key: 'tiqianStudentData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            if (ret.tiqianStudentData.length === 0) {
                console.log("setdefault")
                this.props.setDefault(this.props.studentData)
            } else {
                console.log("setBuffer")
                this.props.setTiqianStudentData(ret.tiqianStudentData)
            }
        }).catch(err => {
            console.log(err)
            this.props.setDefault(this.props.studentData)
        });

    }

    render() {
        if (this.props.studentData.length === 0) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>规则条件 - 提前分班</TitleText>
                    <Link to="/home/xuesheng"><TiqianDefault /></Link>
                    <Link to="/home/jiaoshi"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                    <Link to="/home/shijian"><NextStep className="jieci">下一步</NextStep></Link>
                </HomeMainWrapper>
            </div>
        } else {
            let filterStudentData = this.setFilterData()
            let isWuliSelected, isHuaxueSelected, isShengwuSelected, isLishiSelected, isDiliSelected, isZhengzhiSelected
            if (this.props.selectWuli) {
                isWuliSelected = "selected"
            } else {
                isWuliSelected = "default"
            }
            if (this.props.selectHuaxue) {
                isHuaxueSelected = "selected"
            } else {
                isHuaxueSelected = "default"
            }
            if (this.props.selectShengwu) {
                isShengwuSelected = "selected"
            } else {
                isShengwuSelected = "default"
            }
            if (this.props.selectLishi) {
                isLishiSelected = "selected"
            } else {
                isLishiSelected = "default"
            }
            if (this.props.selectDili) {
                isDiliSelected = "selected"
            } else {
                isDiliSelected = "default"
            }
            if (this.props.selectZhengzhi) {
                isZhengzhiSelected = "selected"
            } else {
                isZhengzhiSelected = "default"
            }
            const rowSelection = {
                selectedRowKeys: this.props.selectedRowKeys,
                onChange: (selectedRowKeys, selectedRows) => {
                    this.props.recordSelectedRows(selectedRows, selectedRowKeys)
                },
                getCheckboxProps: record => ({
                    // disabled: record.name === 'Disabled User', // Column configuration not to be checked
                    // name: record.name,
                }),
            }
            return <div>
                <HomeTopWrapper>
                    <TitleLine />
                    <TitleText>规则条件 - 提前分班</TitleText>
                    <Popover mouseEnterDelay={1.5} content="1、选择未分班的数据 2、在此填写标签名 3、点击分为一班按钮" placement="bottomLeft" overlayStyle={{ fontSize: 14 }}>
                        <FenbanWrapper>
                            <FenbanInput placeholder='请输入该组预分班级的标签名称' innerRef={(input) => { this.tagValue = input }} />
                            <span className="selected" style={{ fontSize: 16 }} onClick={() => this.props.setTag(this.tagValue, this.props.selectedRows, this.props.tiqianStudentData)}>分为一班</span>
                        </FenbanWrapper>
                    </Popover>
                    <Popover mouseEnterDelay={1.5} content="1、选择已分班的数据 2、点击取消分班按钮" placement="bottomRight" overlayStyle={{ fontSize: 14 }}>
                        <DefaultSpan onClick={() => this.props.setDefaultTag(this.props.selectedRows, this.props.tiqianStudentData)}><Divider type="vertical" className="divider" />取消分班</DefaultSpan>
                    </Popover>
                </HomeTopWrapper>
                <HomeButtomWrapper className="hasItem">
                    <Table
                        rowKey="id"
                        className="table"
                        rowSelection={rowSelection}
                        dataSource={filterStudentData}
                        columns={this.props.tiqianStudentColumn}
                        bordered={true}
                        pagination={{
                            simple: true,
                            position: 'top',
                            showQuickJumper: true,
                            hideOnSinglePage: true,
                            total: filterStudentData.length,
                        }}
                    >
                    </Table>
                    <SelectHint>{this.props.selectedRows.length}项</SelectHint>
                    <FilterWrapper>
                        <Divider type="vertical" className="divider2" />
                        <p className="title">科目筛选:</p>
                        <span className={isWuliSelected} onClick={() => { this.props.clickFilter(1, !this.props.selectWuli) }}>物理</span>
                        <Divider type="vertical" className="divider" />
                        <span className={isHuaxueSelected} onClick={() => { this.props.clickFilter(2, !this.props.selectHuaxue) }}>化学</span>
                        <Divider type="vertical" className="divider" />
                        <span className={isShengwuSelected} onClick={() => { this.props.clickFilter(3, !this.props.selectShengwu) }}>生物</span>
                        <Divider type="vertical" className="divider" />
                        <span className={isLishiSelected} onClick={() => { this.props.clickFilter(4, !this.props.selectLishi) }}>历史</span>
                        <Divider type="vertical" className="divider" />
                        <span className={isDiliSelected} onClick={() => { this.props.clickFilter(5, !this.props.selectDili) }}>地理</span>
                        <Divider type="vertical" className="divider" />
                        <span className={isZhengzhiSelected} onClick={() => { this.props.clickFilter(6, !this.props.selectZhengzhi) }}>政治</span>
                    </FilterWrapper>
                    <Link to="/home/jiaoshi"><PreStep className="xuesheng hasItemStep">上一步</PreStep></Link>
                    <StepLine className="xuesheng hasItemLine" />
                    <Link to="/home/shijian"><NextStep className="xuesheng hasItemStep">下一步</NextStep></Link>
                </HomeButtomWrapper>
            </div>
        }
    }

    setFilterData() {
        let judgeData = []
        let tempData = []
        if (this.props.selectWuli) {
            judgeData.push("物理")
        }
        if (this.props.selectHuaxue) {
            judgeData.push("化学")
        }
        if (this.props.selectShengwu) {
            judgeData.push("生物")
        }
        if (this.props.selectLishi) {
            judgeData.push("历史")
        }
        if (this.props.selectDili) {
            judgeData.push("地理")
        }
        if (this.props.selectZhengzhi) {
            judgeData.push("政治")
        }

        if (judgeData.length < 3) {
            message.warning('请至少选择三个筛选科目～')
            return this.props.tiqianStudentData
        } else {
            for (var i = 0; i < this.props.tiqianStudentData.length; i++) {
                let d1 = this.props.tiqianStudentData[i]['subject1']
                let d2 = this.props.tiqianStudentData[i]['subject2']
                let d3 = this.props.tiqianStudentData[i]['subject3']

                if (judgeData.indexOf(d1) > -1 && judgeData.indexOf(d2) > -1 && judgeData.indexOf(d3) > -1) {
                    tempData = tempData.concat(this.props.tiqianStudentData[i])
                }
            }
            return tempData
        }
    }

}





const mapStateToProps = (state) => {
    return {
        studentData: state.getIn(['home', 'studentData']).toJS(),
        tiqianStudentData: state.getIn(['home', 'tiqianStudentData']).toJS(),
        tiqianStudentColumn: state.getIn(['home', 'tiqianStudentColumn']).toJS(),
        selectWuli: state.getIn(['home', 'selectWuli']),
        selectHuaxue: state.getIn(['home', 'selectHuaxue']),
        selectShengwu: state.getIn(['home', 'selectShengwu']),
        selectLishi: state.getIn(['home', 'selectLishi']),
        selectDili: state.getIn(['home', 'selectDili']),
        selectZhengzhi: state.getIn(['home', 'selectZhengzhi']),
        selectedRows: state.getIn(['home', 'selectedRows']).toJS(),
        selectedRowKeys: state.getIn(['home', 'selectedRowKeys']).toJS(),

    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setTiqianStudentData(data) {
            dispatch(actionCreators.handleTiqianStudentDataChangeAction(data))
        },
        setStudentData(data) {
            dispatch(actionCreators.handleStudentFileUploadAction(data, true))
        },
        setDefault(data) {
            let tempTiqianStudentData = data
            for (var i = 0; i < tempTiqianStudentData.length; i++) {
                tempTiqianStudentData[i]['class'] = "暂无"
            }
            dispatch(actionCreators.handleTiqianStudentDataChangeAction(tempTiqianStudentData))
        },
        clickFilter(id, value) {
            dispatch(actionCreators.clickFilterAction(id, value))
        },
        recordSelectedRows(arr, keyArr) {
            dispatch(actionCreators.recordSelectedRowsAction(arr, keyArr))
        },
        setTag(tagValue, selectedRows, data) {
            if (tagValue.value !== "" && selectedRows.length !== 0) {
                let tempTiqianStudentData = data

                for (var i = 0; i < selectedRows.length; i++) {
                    for (var j = 0; j < tempTiqianStudentData.length; j++) {
                        if (tempTiqianStudentData[j]['id'] === selectedRows[i]['id']) {
                            tempTiqianStudentData[j]['class'] = tagValue.value
                        }
                    }
                }
                dispatch(actionCreators.handleTiqianStudentDataChangeAction(tempTiqianStudentData))
            } else {
                message.warning('请先选择数据并填写标签名')
            }
        },
        setDefaultTag(selectedRows, data) {
            if (selectedRows.length !== 0) {
                let tempTiqianStudentData = data
                for (var i = 0; i < selectedRows.length; i++) {
                    for (var j = 0; j < tempTiqianStudentData.length; j++) {
                        if (tempTiqianStudentData[j]['id'] === selectedRows[i]['id']) {
                            tempTiqianStudentData[j]['class'] = "暂无"
                        }
                    }
                }
                dispatch(actionCreators.handleTiqianStudentDataChangeAction(tempTiqianStudentData))
            } else {
                message.warning('请先选择需要取消分班的数据')
            }
        }
    }
}



export default connect(mapStateToProps, mapDispatchToProps)(Tiqian)
