import React from 'react'
import { connect } from 'react-redux'
import { SignupWrapper, Input, LogoWrapper, LogoTitle, Logo, SignupBox, SignupTitle, BackgroundDecorate1, BackgroundDecorate2, SignupFooter, SignupMainTitle, SignupSubTitle, SignupText } from './style'
import { actionCreators } from './store'
import { actionCreators as loginActionCreators } from '../login/store'
import { Link, Redirect } from 'react-router-dom'
import { Button } from 'antd'


class Signup extends React.Component {
    render() {
        if (!this.props.loginStatus) {
            return (
                <SignupWrapper>
                    <BackgroundDecorate2 /><BackgroundDecorate1 />
                    <Link to="/signup">
                        <LogoWrapper>
                            <Logo></Logo>
                            <LogoTitle>简道 智能排课系统</LogoTitle>
                        </LogoWrapper>
                    </Link>
                    <SignupSubTitle>INTELLIGENT COURSE SCHEDULE SYSTEM</SignupSubTitle>
                    <SignupMainTitle>
                        <div>欢迎使用</div>
                        <div>简道智能排课系统…</div>
                    </SignupMainTitle>

                    <SignupBox>
                        <SignupTitle>简道账号注册（教务员）</SignupTitle>
                        <Input className="mySignpup" placeholder='请输入用户名' innerRef={(input) => { this.account = input }}></Input>
                        <Input className="mySignpup" placeholder='请输入密码' type='password' innerRef={(input) => { this.password = input }}></Input>
                        <Input className="mySignpup" placeholder='请再次输入密码' type='password' ></Input>
                        <Button type="primary" className="button" onClick={() => this.props.signup(this.account, this.password)}>注册并登录</Button>
                        <Link to="/login">
                            <SignupText>已有账号？直接前往登录</SignupText>
                        </Link>
                    </SignupBox>
                    <SignupFooter>
                        <span>2019 &#169; made by  </span>
                        <span className="chucktzh">  Chucktzh</span>
                    </SignupFooter>
                </SignupWrapper>
            )
        } else {
            return <Redirect to='/instructor' />
        }
    }

}


const mapState = (state) => ({
    loginStatus: state.getIn(['login', 'login'])
})

const mapDispatch = (dispatch) => ({
    signup(account, password) {
        dispatch(loginActionCreators.login(account.value, password.value))
        window.storage.save({
            key: 'userName',
            data: {
                name: account.value
            }
        })
    }
})

export default connect(mapState, mapDispatch)(Signup)