import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { actionCreators } from './store'
import {
    InstructorWrapper, InstructorSubTitle, InstructorMainTitle, ProcessDecorate1, IsometricDecorate1, ProcessDecorate2, IsometricDecorate2, ProcessDecorate3, IsometricDecorate3, ProcessDecorate4, IsometricDecorate4, TeacherProcessDecorate3, TeacherProcessDecorate2, TeacherProcessDecorate1, TeacherIsometricDecorate3, TeacherIsometricDecorate2, TeacherIsometricDecorate1
} from './style';
import { Button } from 'antd'
import QueueAnim from 'rc-queue-anim';


class Help extends React.Component {
    render() {
        if (this.props.userName.indexOf("teacher") != -1) {
            return <div>
                <InstructorWrapper>
                    <QueueAnim
                        type={this.props.infoType}
                        duration={600}
                    >
                        <InstructorMainTitle key="a">简道</InstructorMainTitle>
                        <InstructorSubTitle key="b">
                            <div>从接收通知到协同排课…</div>
                            <div>只需三步，简道助您掌控属于自己的课表！</div>
                        </InstructorSubTitle>
                        <Link to="/help" key="bangzhu">
                            <Button type="default" className="helpButton">帮助文档</Button>
                        </Link>
                        <Link to="/home/tongzhi" key="kaishi">
                            <Button type="primary" className="homeButton">前往协同排课</Button>
                        </Link>
                        {(() => {
                            switch (this.props.stepic) {
                                case 'one':
                                    return <div>
                                        <QueueAnim
                                            type={this.props.stepType}
                                            duration={this.props.stepDuration}
                                            delay={this.props.stepDelay}
                                        >
                                            <TeacherProcessDecorate1 key="d1" />
                                        </QueueAnim>
                                        <QueueAnim
                                            type={this.props.isometricType1}
                                            duration={this.props.isometricDuration1}
                                            delay={this.props.isometricDelay}
                                        >
                                            <TeacherIsometricDecorate1 key="i1" />
                                        </QueueAnim>
                                    </div>;
                                case 'two':
                                    return <div>
                                        <QueueAnim
                                            type={this.props.stepType}
                                            duration={this.props.stepDuration}
                                            delay={this.props.stepDelay}
                                        >
                                            <TeacherProcessDecorate2 key="d2" />
                                        </QueueAnim>
                                        <QueueAnim
                                            type={this.props.isometricType2}
                                            duration={this.props.isometricDuration2}
                                            delay={this.props.isometricDelay}
                                        >
                                            <TeacherIsometricDecorate2 key="i2" />
                                        </QueueAnim>
                                    </div>;
                                case 'three':
                                    return <div>
                                        <QueueAnim
                                            type={this.props.stepType}
                                            duration={this.props.stepDuration}
                                            delay={this.props.stepDelay}
                                        >
                                            <TeacherProcessDecorate3 key="d3" />
                                        </QueueAnim>
                                        <QueueAnim
                                            type={this.props.isometricType3}
                                            duration={this.props.isometricDuration3}
                                            delay={this.props.isometricDelay}
                                        >
                                            <TeacherIsometricDecorate3 key="i3" />
                                        </QueueAnim>
                                    </div>;
                                default:
                                    return null;
                            }
                        })()}
                    </QueueAnim>
                </InstructorWrapper>
            </div>
        } else {
            return <div>
                <InstructorWrapper>
                    <QueueAnim
                        type={this.props.infoType}
                        duration={600}
                    >
                        <InstructorMainTitle key="a">简道</InstructorMainTitle>
                        <InstructorSubTitle key="b">
                            <div>从填写基础信息到查询导出…</div>
                            <div>只需四步，简道助您排出高质量的课程表!</div>
                        </InstructorSubTitle>
                        <Link to="/help" key="bangzhu">
                            <Button type="default" className="helpButton">帮助文档</Button>
                        </Link>
                        <Link to="/home/jieci" key="kaishi">
                            <Button type="primary" className="homeButton">开始排课</Button>
                        </Link>
                        {(() => {
                            switch (this.props.stepic) {
                                case 'one':
                                    return <div>
                                        <QueueAnim
                                            type={this.props.stepType}
                                            duration={this.props.stepDuration}
                                            delay={this.props.stepDelay}
                                        >
                                            <ProcessDecorate1 key="d1" />
                                        </QueueAnim>
                                        <QueueAnim
                                            type={this.props.isometricType1}
                                            duration={this.props.isometricDuration1}
                                            delay={this.props.isometricDelay}
                                        >
                                            <IsometricDecorate1 key="i1" />
                                        </QueueAnim>
                                    </div>;
                                case 'two':
                                    return <div>
                                        <QueueAnim
                                            type={this.props.stepType}
                                            duration={this.props.stepDuration}
                                            delay={this.props.stepDelay}
                                        >
                                            <ProcessDecorate2 key="d2" />
                                        </QueueAnim>
                                        <QueueAnim
                                            type={this.props.isometricType2}
                                            duration={this.props.isometricDuration2}
                                            delay={this.props.isometricDelay}
                                        >
                                            <IsometricDecorate2 key="i2" />
                                        </QueueAnim>
                                    </div>;
                                case 'three':
                                    return <div>
                                        <QueueAnim
                                            type={this.props.stepType}
                                            duration={this.props.stepDuration}
                                            delay={this.props.stepDelay}
                                        >
                                            <ProcessDecorate3 key="d3" />
                                        </QueueAnim>
                                        <QueueAnim
                                            type={this.props.isometricType3}
                                            duration={this.props.isometricDuration3}
                                            delay={this.props.isometricDelay}
                                        >
                                            <IsometricDecorate3 key="i3" />
                                        </QueueAnim>
                                    </div>;
                                case 'four':
                                    return <div>
                                        <QueueAnim
                                            type={this.props.stepType}
                                            duration={this.props.stepDuration}
                                            delay={this.props.stepDelay}
                                        >
                                            <ProcessDecorate4 key="d4" />
                                        </QueueAnim>
                                        <QueueAnim
                                            type={this.props.isometricType4}
                                            duration={this.props.isometricDuration4}
                                            delay={this.props.isometricDelay}
                                        >
                                            <IsometricDecorate4 key="i4" />
                                        </QueueAnim>
                                    </div>;
                                default:
                                    return null;
                            }
                        })()}
                    </QueueAnim>
                </InstructorWrapper>
            </div>
        }

    }

    componentDidMount() {
        if (this.props.userName.indexOf("teacher") != -1) {
            this.interval = setInterval(
                () => {
                    this.props.enterAnimCallback()
                    if (this.props.stepic === "one") {
                        this.props.changeStepic("two")
                    } else if (this.props.stepic === "two") {
                        this.props.changeStepic("three")
                    } else if (this.props.stepic === "three") {
                        this.props.changeStepic("one")
                    } 
                },
                4000
            )
        } else {
            this.interval = setInterval(
                () => {
                    this.props.enterAnimCallback()
                    if (this.props.stepic === "one") {
                        this.props.changeStepic("two")
                    } else if (this.props.stepic === "two") {
                        this.props.changeStepic("three")
                    } else if (this.props.stepic === "three") {
                        this.props.changeStepic("four")
                    } else if (this.props.stepic === "four") {
                        this.props.changeStepic("one")
                    }
                },
                4000
            )
        }


    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }
}


const mapState = (state) => ({
    userName: state.get('header').get('userName'),

    enterAnim: state.getIn(['instructor', 'enterAnim']),
    infoType: state.getIn(['instructor', 'infoType']),
    stepic: state.getIn(['instructor', 'stepic']),
    stepType: state.getIn(['instructor', 'stepType']),
    stepDelay: state.getIn(['instructor', 'stepDelay']),
    stepDuration: state.getIn(['instructor', 'stepDuration']),
    isometricDelay: state.getIn(['instructor', 'isometricDelay']),
    isometricType1: state.getIn(['instructor', 'isometricType1']),
    isometricType2: state.getIn(['instructor', 'isometricType2']),
    isometricType3: state.getIn(['instructor', 'isometricType3']),
    isometricType4: state.getIn(['instructor', 'isometricType4']),
    isometricDuration1: state.getIn(['instructor', 'isometricDuration1']),
    isometricDuration2: state.getIn(['instructor', 'isometricDuration2']),
    isometricDuration3: state.getIn(['instructor', 'isometricDuration3']),
    isometricDuration4: state.getIn(['instructor', 'isometricDuration4']),
})

const mapDispatch = (dispatch) => ({
    enterAnimCallback() {
        dispatch(actionCreators.setEnterAnimStatusAction())
    },
    changeStepic(stepkey) {
        dispatch(actionCreators.changeStepicAction(stepkey))
    }
})

export default connect(mapState, mapDispatch)(Help)


