import { fromJS } from 'immutable'
import * as constants from './constants'


const defaultState = fromJS({
    enterAnim: true,
    infoType: "right",
    stepic: "one",
    stepType: "left",
    stepDuration: 1200,
    stepDelay: 500,
    isometricType1: "top",
    isometricType2: "top",
    isometricType3: "top",
    isometricType4: "top",
    isometricDelay: 1000,
    isometricDuration1: 2000,
    isometricDuration2: 2000,
    isometricDuration3: 2000,
    isometricDuration4: 2000,
})

export default (state = defaultState, action) => {

    switch (action.type) {
        case constants.CHANGE_ENTER_ANIM:
            return state.merge({
                'enterAnim': action.value,
                'infoType': "top",
                'stepType': "alpha",
                'stepDuration': 3000,
                'stepDelay': 0,
                'isometricDelay': 0,
            })
        case constants.CHANGE_STEPIC:
            if (action.value === "one") {
                return state.merge({
                    'isometricType1': "bottom",
                    'isometricType2': "alpha",
                    'isometricType3': "alpha",
                    'isometricType4': "alpha",
                    'isometricDuration1': 1500,
                    'isometricDuration2': 1500,
                    'isometricDuration3': 1500,
                    'isometricDuration4': 0,
                    'stepic': action.value
                })
            } else if (action.value === "two") {
                return state.merge({
                    'isometricType1': "alpha",
                    'isometricType2': "bottom",
                    'isometricType3': "alpha",
                    'isometricType4': "alpha",
                    'isometricDuration1': 0,
                    'isometricDuration2': 1500,
                    'isometricDuration3': 1500,
                    'isometricDuration4': 1500,
                    'stepic': action.value
                })
            } else if (action.value === "three") {
                return state.merge({
                    'isometricType1': "alpha",
                    'isometricType2': "alpha",
                    'isometricType3': "bottom",
                    'isometricType4': "alpha",
                    'isometricDuration1': 1500,
                    'isometricDuration2': 0,
                    'isometricDuration3': 1500,
                    'isometricDuration4': 1500,
                    'stepic': action.value
                })
            } else if (action.value === "four") {
                return state.merge({
                    'isometricType1': "alpha",
                    'isometricType2': "alpha",
                    'isometricType3': "alpha",
                    'isometricType4': "bottom",
                    'isometricDuration1': 1500,
                    'isometricDuration2': 1500,
                    'isometricDuration3': 0,
                    'isometricDuration4': 1500,
                    'stepic': action.value
                })
            }

        default:
            return state
    }

}