import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import 'immutable'
import * as XLSX from 'xlsx';
import {
    TitleLine, TitleText, HomeTopWrapper, HomeButtomWrapper, TopHint, StudentDefault, NextStep, PreStep, StepLine, TotalData
} from '../style';
import { Button, Table } from 'antd'
import { Link } from 'react-router-dom'

class Xuesheng extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'studentData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setStudentData(ret.studentData)
        }).catch(err => {
            console.log(err)
        });
    }

    render() {
        if (this.props.studentData.length !== 0) {
            return <div>
                <HomeTopWrapper>
                    <TitleLine />
                    <TitleText>基础信息 - 学生信息</TitleText>
                    <TopHint>Tips：对表格填写有疑问？<Link to="/help" onClick={this.props.handleAnchor}><span className="link">点击查看帮助文档</span></Link></TopHint>
                    <Button type="default" className="templeteButton"><a className="download" href="/api/Student.xlsx" download="Student.xlsx">模板下载</a></Button>
                    <input ref="inputButton" type='file' accept='.xlsx, .xls' className="inputButton" onChange={this.props.handleStudentFileUpload} />
                    <Button type="primary" className="uploadButton" onClick={this.handleButton.bind(this)}>重新上传</Button>
                </HomeTopWrapper>
                <HomeButtomWrapper className="hasItem">
                    <Table
                        rowKey="id"
                        className="table"
                        dataSource={this.props.studentData}
                        columns={this.props.studentColumn}
                        bordered={true}
                        pagination={{
                            simple: true,
                            position: 'top',
                            showQuickJumper: true,
                            hideOnSinglePage: true,
                            total: this.props.studentData.length,
                            hideOnSinglePage: false
                        }}
                    >
                    </Table>
                    <TotalData>学生信息（共{this.props.studentData.length}条）</TotalData>
                    <Link to="/home/jieci"><PreStep className="hasItemStep">上一步</PreStep></Link>
                    <StepLine className="hasItemLine" />
                    <Link to="/home/jiaoshi"><NextStep className="hasItemStep">下一步</NextStep></Link>
                </HomeButtomWrapper>
            </div>
        } else {
            return <div>
                <HomeTopWrapper>
                    <TitleLine />
                    <TitleText>基础信息 - 学生信息</TitleText>
                    <TopHint>Tips：对表格填写有疑问？<Link to="/help" onClick={this.props.handleAnchor}><span className="link">点击查看帮助文档</span></Link></TopHint>
                    <Button type="default" className="templeteButton"><a className="download" href="/api/Student.xlsx" download="Student.xlsx">模板下载</a></Button>
                    <input ref="inputButton" type='file' accept='.xlsx, .xls' className="inputButton" onChange={this.props.handleStudentFileUpload} />
                    <Button type="primary" className="uploadButton" onClick={this.handleButton.bind(this)}>上传信息</Button>
                </HomeTopWrapper>
                <HomeButtomWrapper>
                    <StudentDefault />
                    <Link to="/home/jieci"><PreStep className="xuesheng">上一步</PreStep></Link>
                    <StepLine className="xuesheng" />
                    <Link to="/home/jiaoshi"><NextStep className="xuesheng">下一步</NextStep></Link>
                </HomeButtomWrapper>
            </div>
        }


    }

    handleButton() {
        this.refs.inputButton.click();
    }
}


const mapStateToProps = (state) => {
    return {
        studentData: state.getIn(['home', 'studentData']).toJS(),
        studentColumn: state.getIn(['home', 'studentColumn']).toJS(),
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleStudentFileUpload(e) {
            var rABS = true;
            var files = e.target.files, f = files[0]
            var reader = new FileReader()
            reader.onload = function (e) {
                var data = e.target.result
                if (!rABS) data = new Uint8Array(data);
                var workbook = XLSX.read(data, { type: rABS ? 'binary' : 'array' })

                /* DO SOMETHING WITH workbook HERE */
                let first_worksheet = workbook.Sheets[workbook.SheetNames[0]];
                let jsonArr = XLSX.utils.sheet_to_json(first_worksheet, { header: 1 });
                let tempData = []
                for (var i = 0; i < jsonArr.length; i++) {
                    let tempArray = [{
                        id: jsonArr[i][0],
                        name: jsonArr[i][1],
                        class: jsonArr[i][2],
                        subject1: jsonArr[i][3],
                        subject2: jsonArr[i][4],
                        subject3: jsonArr[i][5],
                        score: jsonArr[i][6],
                    }]
                    tempData = tempData.concat(tempArray)
                }
                dispatch(actionCreators.handleStudentFileUploadAction(tempData, false))
            }
            if (rABS) reader.readAsBinaryString(f); else reader.readAsArrayBuffer(f)
        },
        setStudentData(data) {
            dispatch(actionCreators.handleStudentFileUploadAction(data, true))
        },
        handleAnchor() {
            let type = 1;
            dispatch(actionCreators.handleAnchorAction(type))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Xuesheng)
