import React from 'react'
import { connect } from 'react-redux'
import { actionCreators as homeActionCreators } from '../home/store'

import Footer2 from '../../commen/footer2'
import helpPic1 from '../../statics/help/helppic1.png'
import helpPic2 from '../../statics/help/helppic2.png'
import helpPic3 from '../../statics/help/helppic3.png'
import helpPic4 from '../../statics/help/helppic4.png'
import helpPic5 from '../../statics/help/helppic5.png'
import helpPic6 from '../../statics/help/helppic6.png'
import helpPic7 from '../../statics/help/helppic7.png'
import helpPic8 from '../../statics/help/helppic8.png'
import helpPic9 from '../../statics/help/helppic9.png'
import helpPic10 from '../../statics/help/helppic10.png'
import helpPic11 from '../../statics/help/helppic11.png'
import helpPic12 from '../../statics/help/helppic12.png'

import picHelp1 from '../../statics/help/pichelp1.png'
import picHelp2 from '../../statics/help/pichelp2.png'
import picHelp3 from '../../statics/help/pichelp3.png'
import picHelp4 from '../../statics/help/pichelp4.png'
import picHelp5 from '../../statics/help/pichelp5.png'
import { BackTop, Typography, Anchor } from 'antd';
import {
    HelpWrapper, BacktopButton, ContentWrapper, HelpPic, MyH1, MyBlockquote, MyH3, MyH4, MyP, MyOl, MyLi
} from './style';




class Help extends React.Component {
    render() {
        if (this.props.userName.indexOf("teacher") != -1) {
            return <div>
                <HelpWrapper>
                    <BackTop><BacktopButton /></BackTop>
                    <Anchor className="myAnchor" affix={true} onClick={this.handleAnchorClick.bind(this)}>
                        <Anchor.Link href="#toc_15" title="1 通知动态"></Anchor.Link>
                        <Anchor.Link href="#toc_16" title="2 协同排课"></Anchor.Link>
                        <Anchor.Link href="#toc_17" title="3 查看课表"></Anchor.Link>
                    </Anchor>
                    <ContentWrapper>
                        <MyH1 id="toc_14">简道智能排课系统帮助文档</MyH1>
                        <MyBlockquote>
                            <MyP>欢迎使用简道智能排课系统！</MyP>
                            <MyP>仔细阅读并遵循以下三个步骤，您将能够把自己的个性化排课需求同步给教务员！</MyP>
                        </MyBlockquote>
                        <MyH3 id="toc_15">1 接收排课通知</MyH3>
                        <MyP>当教务员未开启协同排课功能时，进入我的排课模块，您将看到通知动态如下图，此时无法协同排课。</MyP>
                        <HelpPic src={picHelp1} />
                        <MyP>当教务员开启协同排课功能时，进入我的排课模块，您将看到通知动态如下图，点击即可前往协同排课。</MyP>
                        <HelpPic src={picHelp2} />
                        <MyH3 id="toc_16">2 前往协同排课</MyH3>
                        <MyP>当协同排课功能开启时，您可以在此页面中，设置自己的不排课时间意愿，从而满足您的个性化需求，具体步骤如下：</MyP>
                        <MyOl>
                            <MyLi>点击对应的格子，将该时间段排除在外</MyLi>
                        </MyOl>
                        <HelpPic src={picHelp3} />
                        <MyP><strong>注意！您的时间安排意愿将自动同步给教务员，教务员会进行调整，请以最终课表为准</strong></MyP>
                        <MyH3 id="toc_17">3 查看导出课表</MyH3>
                        <MyP>当教务员未完成排课流程时，您将无法看到课表，如有疑问请与教务员联系。</MyP>
                        <HelpPic src={picHelp4} />
                        <MyP>当教务员完成排课流程时，您将在此处看到您的课表，点击下载课表按钮，即可将课表下载到本地。</MyP>
                        <HelpPic src={picHelp5} />
                    </ContentWrapper>
                    <Footer2 />
                </HelpWrapper>
            </div>
        } else {
            return <div>
                <HelpWrapper>
                    <BackTop><BacktopButton /></BackTop>
                    <Anchor className="myAnchor" affix={true} onClick={this.handleAnchorClick.bind(this)}>
                        <Anchor.Link href="#toc_1" title="1 基础信息">
                            <Anchor.Link href="#toc_2" title="1.1 节次设置" />
                            <Anchor.Link href="#toc_3" title="1.2 学生信息" />
                            <Anchor.Link href="#toc_4" title="1.3 教师信息" />
                        </Anchor.Link>
                        <Anchor.Link href="#toc_5" title="2 规则条件">
                            <Anchor.Link href="#toc_6" title="2.1 提前排课" />
                            <Anchor.Link href="#toc_7" title="2.2 时间安排" />
                        </Anchor.Link>
                        <Anchor.Link href="#toc_8" title="3 自动排课"></Anchor.Link>
                        <Anchor.Link href="#toc_9" title="4 查看课表"></Anchor.Link>
                    </Anchor>
                    <ContentWrapper>
                        <MyH1 id="toc_0">简道智能排课系统帮助文档</MyH1>
                        <MyBlockquote>
                            <MyP>欢迎使用简道智能排课系统！</MyP>
                            <MyP>仔细阅读并遵循以下四个步骤，您将很快的排出高质量的课表！</MyP>
                        </MyBlockquote>
                        <MyH3 id="toc_1">1 填写基础信息</MyH3>
                        <MyP>第一步，在此模块的三个子页面中，您需要填写与教学安排相关【节次数据】，下载模板并填写【学生信息】与【教师信息】，上传至对应的页面。</MyP>
                        <MyP>当您完成此步骤后，如果没有其余特殊的排课要求，可以直接前往自动排课，将后面的工作交给我们；当然，继续完成步骤二【补充规则条件】，有助于提高课表的质量与满足个性化需求。</MyP>
                        <MyH4 id="toc_2">1.1 节次设置</MyH4>
                        <MyP>您需要在此页面填写六项基本数据。</MyP>
                        <MyOl>
                            <MyLi><MyP>每周上课天数：指教学安排中每一周允许排课的天数，一般为5至6天</MyP></MyLi>
                            <MyLi><MyP>每天上课节数：指教学安排中每一天允许排课的课程节数，一般为8至12节</MyP></MyLi>
                            <MyLi><MyP>现有教室数量：指可用以本次排课的教室数量，根据学校实际情况填写</MyP></MyLi>
                            <MyLi><MyP>教室容纳人数：指单个教室所能容纳的学生人数，根据教室实际情况填写</MyP></MyLi>
                            <MyLi><MyP>行政班级数量：指本次排课最终所需分出的班级数量，根据教学安排情况填写</MyP></MyLi>
                            <MyLi><MyP>班级最小人数：指完成排课后每个班级所分配的最少人数，此项数值应保持在一个合理的范围内（学生总数/行政班级数量 * 80%～120%），过大或过小都有可能导致无法生成课表</MyP></MyLi>
                        </MyOl>
                        <HelpPic src={helpPic1} />
                        <MyP>此部分的信息均为必填项，如果您对数据项有疑问，鼠标短暂悬停后将出现对应项的文字提示气泡。</MyP>
                        <MyH4 id="toc_3">1.2 学生信息</MyH4>
                        <MyP>您需要在此页面上传学生信息,将学生的学号、所选科目等信息提供给我们。</MyP>
                        <MyP>首先，点击下图中【下载模板按钮】</MyP>
                        <HelpPic src={helpPic2} />
                        <MyP>您将会得到一个Excel表格，里面包含了示例数据。按列依次是：学号、姓名、当前班别、所选科目、所选科目的考试总分。</MyP>
                        <HelpPic src={helpPic3} />
                        <MyP>将示例数据换成本校的学生信息数据并上传（图中箭头二所示按钮），上传成功后您将能在本页面查看到所有的学生信息数据。</MyP>
                        <HelpPic src={helpPic4} />
                        <MyH4 id="toc_4">1.3 教师信息</MyH4>
                        <MyP>您需要在此页面上传教师信息，与上传学生信息唯一不同的是，此处所需的教师信息数据更少，如下图所示：</MyP>
                        <HelpPic src={helpPic5} />
                        <MyP>依次填写工号，姓名、所授科目后，上传表格。当看到页面上呈现教师信息时，您就完成了全部基础信息的填写！</MyP>
                        <MyH3 id="toc_5">2 补充规则条件</MyH3>
                        <MyP>第二步，在此模块的两个子页面中，您可以视情况将部分学生提前分为一班、设置课程或教师的不排课时间，以获得质量更高、更为个性化的课表。</MyP>
                        <MyH4 id="toc_6">2.1 提前分班</MyH4>
                        <MyP>您可以在此页面中，将满足特定规则的学生提前分为一班（如成绩较好的学生提前分入重点班），只需三步：</MyP>
                        <MyOl>
                            <MyLi><MyP>勾选学生信息行</MyP></MyLi>
                            <MyLi><MyP>填写该班级的标签名</MyP></MyLi>
                            <MyLi><MyP>点击分为一班按钮</MyP></MyLi>
                        </MyOl>
                        <HelpPic src={helpPic6} />
                        <MyP>此外,我们提供了诸多便于您筛选出符合特定规则学生的功能，例如：</MyP>
                        <MyOl>
                            <MyLi><MyP>按所选科目进行筛选</MyP></MyLi>
                            <MyLi><MyP>按总分、学号等进行排序</MyP></MyLi>
                            <MyLi><MyP>全选/反选一页数据</MyP></MyLi>
                        </MyOl>
                        <HelpPic src={helpPic7} />
                        <MyP>如果您想修改先前的分班操作，选中已分班的数据项，点击取消分班即可。</MyP>
                        <MyH4 id="toc_7">2.2 时间安排</MyH4>
                        <MyP>您可以在此页面中，为某一科目或某一名教师，单独的设置他们的不排课时间，从而满足教学需求或老师的个性化需求，具体步骤如下：</MyP>
                        <MyOl>
                            <MyLi><MyP>选择一个分类方式（科目或教师）</MyP></MyLi>
                            <MyLi><MyP>选择一个具体的科目或老师</MyP></MyLi>
                            <MyLi><MyP>点击对应的格子，将该时间段排除在外</MyP></MyLi>
                        </MyOl>
                        <HelpPic src={helpPic8} />
                        <MyP>此外，在此页面中，您可以开启协同排课，让教师们自由的按意愿填写不排课的时间，只需点击一下按钮即可开启协同，教师们的操作结果会实时的在对应表格中反馈出来。</MyP>
                        <HelpPic src={helpPic9} />
                        <MyP><strong>注意！点击按钮后，所有的教师均可以设置他们的不排课时间（而非当前选中教师）</strong></MyP>
                        <MyH3 id="toc_8">3 一键自动排课</MyH3>
                        <MyP>第三步，当您看到如下图所示页面时，只需点击按钮，我们将帮您完成剩余的冗杂工作！</MyP>
                        <HelpPic src={helpPic10} />
                        <MyP>如果您对排出的课表不满意，调整前序的规则条件后，可以回到此页面点击重新排课。</MyP>
                        <HelpPic src={helpPic11} />
                        <MyH3 id="toc_9">4 查询导出课表</MyH3>
                        <MyP>第四步，完成自动排课后，您可以进行课表查询、查看与导出，具体步骤如下：</MyP>
                        <MyOl>
                            <MyLi><MyP>输入学号或工号</MyP></MyLi>
                            <MyLi><MyP>点击查询按钮</MyP></MyLi>
                            <MyLi><MyP>下载当前课表或打包下载全部课表</MyP></MyLi>
                        </MyOl>
                        <HelpPic src={helpPic12} />
                    </ContentWrapper>
                    <Footer2 />
                </HelpWrapper>
            </div>
        }
    }

    componentDidMount() {
        if (this.props.helpAnchor === 1) {
            document.querySelector("#toc_3").scrollIntoView({
                block: 'start',
                behavior: 'smooth'
            })
        } else if (this.props.helpAnchor === 2) {
            document.querySelector("#toc_4").scrollIntoView({
                block: 'start',
                behavior: 'smooth'
            })
        }
        this.props.handleAnchor()
    }

    handleAnchorClick(e, link) {
        e.preventDefault();
        console.log(link);
    }
}


const mapState = (state) => {
    return {
        userName: state.get('header').get('userName'),
        helpAnchor: state.get('home').get('helpAnchor'),
    }
}

const mapDispatch = (dispatch) => {
    return {
        handleAnchor() {
            let type = 0;
            dispatch(homeActionCreators.handleAnchorAction(type))
        }
    }
}


export default connect(mapState, mapDispatch)(Help)


