import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import {
    TopHint, TitleLine, TitleText, HomeTopWrapper, HomeButtomWrapper, NextStep, PreStep, StepLine, HomeMainWrapper, ShijianDefault, ShijianBothDefault, ShijianBasicDefault, ShijianTeacherDefault, TipsHint, TongzhiDefault
} from '../style';
import { Select, Table, Tag, Button, Switch } from 'antd'
import { Link } from 'react-router-dom'

class Shijian extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'teacherData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setTeacherData(ret.teacherData)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciDay',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciDay(ret.day)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciCourse',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciCourse(ret.course)
        }).catch(err => {
            console.log(err)
        });

        window.storage.load({
            key: 'shijianTeacherData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setShijianTeacherData(ret.shijianTeacherData)
        }).catch(err => {
            console.log(err)
        });

        window.storage.load({
            key: 'shijianSelectOptionOne',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setShijianSelectOptionOne(ret.shijianSelectOptionOne)
        }).catch(err => {
            console.log(err)
        });

        window.storage.load({
            key: 'shijianSelectOptionTwo',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.handleOptionTwoChange(ret.shijianSelectOptionTwo)
        }).catch(err => {
            console.log(err)
        });


        window.storage.load({
            key: 'wuliTimetable',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.changeSubjectTimetable(ret.wuliTimetable, "物理")
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'huaxueTimetable',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.changeSubjectTimetable(ret.huaxueTimetable, "化学")
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'shengwuTimetable',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.changeSubjectTimetable(ret.shengwuTimetable, "生物")
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'lishiTimetable',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.changeSubjectTimetable(ret.lishiTimetable, "历史")
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'diliTimetable',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.changeSubjectTimetable(ret.diliTimetable, "地理")
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'zhengzhiTimetable',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.changeSubjectTimetable(ret.zhengzhiTimetable, "政治")
        }).catch(err => {
            console.log(err)
        });



        window.storage.load({
            key: 'teacherTimetable',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.changeTeacherTimetable(ret.teacherTimetable)
        }).catch(err => {
            console.log(err)
        });

        window.storage.load({
            key: 'canTeacherPaike',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setXietong(ret.canTeacherPaike)
        }).catch(err => {
            console.log(err)
        });
    }

    render() {
        if (this.props.userName.indexOf("teacher") === -1 && (this.props.teacherData.length === 0 && (this.props.day === "" | this.props.course === ""))) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>规则条件 - 时间安排</TitleText>
                    <Link to="/home/jieci"><ShijianBothDefault /></Link>
                    <Link to="/home/tiqian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else if (this.props.userName.indexOf("teacher") === -1 && (this.props.day === "" | this.props.course === "")) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>规则条件 - 时间安排</TitleText>
                    <Link to="/home/jieci"><ShijianBasicDefault /></Link>
                    <Link to="/home/tiqian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else if (this.props.userName.indexOf("teacher") === -1 && this.props.teacherData.length === 0) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>规则条件 - 时间安排</TitleText>
                    <Link to="/home/jiaoshi"><ShijianTeacherDefault /></Link>
                    <Link to="/home/tiqian"><PreStep className="jieci">上一步</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else if (this.props.userName.indexOf("teacher") !== -1 && this.props.canTeacherPaike !== true) {
            return <div>
                <HomeMainWrapper>
                    <TitleLine />
                    <TitleText>协同排课 - 时间安排</TitleText>
                    <TongzhiDefault />
                    <Link to="/instructor"><PreStep className="tongzhi">返回首页</PreStep></Link>
                    <StepLine className="jieci" />
                </HomeMainWrapper>
            </div>
        } else {
            let isOptionTwoDisable = true
            let defaultOptionOne, defaultOptionTwo = undefined
            if (this.props.shijianSelectOptionOne !== "") {
                isOptionTwoDisable = false
                defaultOptionOne = this.props.shijianSelectOptionOne
            } else {
                isOptionTwoDisable = true
            }
            if (this.props.shijianSelectOptionTwo !== "") {
                defaultOptionTwo = this.props.shijianSelectOptionTwo
            }
            return <div>
                <HomeTopWrapper>
                    <TitleLine />

                    {/* 标题 根据用户权限进行变化 */}
                    {(() => {
                        if (this.props.userName.indexOf("teacher") !== -1) {
                            return <TitleText>协同排课 - 时间安排</TitleText>
                        } else {
                            return <TitleText>规则条件 - 时间安排</TitleText>
                        }
                    })()}

                    {/* 选择器/提示/开启协作按钮 根据用户权限进行隐藏/显示 */}
                    {(() => {
                        if (this.props.userName.indexOf("teacher") !== -1) {
                            return <TopHint>Tips：默认为允许排课，点击对应的格子，设置为不排课。结果将自动同步给教务员</TopHint>
                        } else {
                            return <div>
                                <Select
                                    style={{ width: 250 }}
                                    placeholder="请选择分类方式"
                                    optionFilterProp="children"
                                    className="selectMain firstone"
                                    defaultValue={defaultOptionOne}
                                    value={defaultOptionOne}
                                    onChange={(value) => { this.props.handleOptionOneChange(value, this.props.teacherData) }}
                                >
                                    {this.props.shijianClassifyData.map(classify => <Select.Option key={classify}>{classify}</Select.Option>)}
                                </Select>
                                {
                                    (() => {
                                        if (this.props.shijianSelectOptionOne === "教师不排课时间") {
                                            return <Select
                                                showSearch
                                                style={{ width: 250 }}
                                                placeholder="请选择一名老师"
                                                optionFilterProp="children"
                                                className="selectMain secondone"
                                                disabled={isOptionTwoDisable}
                                                defaultValue={defaultOptionTwo}
                                                value={defaultOptionTwo}
                                                onChange={(value) => { this.props.handleOptionTwoChange(value) }}
                                                filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                            >
                                                {this.props.shijianTeacherData.map(teacher => <Select.Option key={teacher['id']}>{teacher['name'] + " | 教工号：" + teacher['id']}</Select.Option>)}
                                            </Select>
                                        } else if (this.props.shijianSelectOptionOne === "科目不排课时间") {
                                            return <Select
                                                showSearch
                                                style={{ width: 250 }}
                                                placeholder="请选择一个科目"
                                                optionFilterProp="children"
                                                className="selectMain secondone"
                                                disabled={isOptionTwoDisable}
                                                defaultValue={defaultOptionTwo}
                                                value={defaultOptionTwo}
                                                onChange={(value) => { this.props.handleOptionTwoChange(value) }}
                                                filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                            >
                                                {this.props.shijianSubjectData.map(subject => <Select.Option key={subject}>{subject}</Select.Option>)}
                                            </Select>
                                        } else {
                                            return <Select
                                                showSearch
                                                style={{ width: 250 }}
                                                placeholder="请选择老师或科目"
                                                optionFilterProp="children"
                                                className="selectMain secondone"
                                                disabled={isOptionTwoDisable}
                                                defaultValue={defaultOptionTwo}
                                                value={defaultOptionTwo}
                                                onChange={(value) => { this.props.handleOptionTwoChange(value) }}
                                                filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                            >
                                            </Select>
                                        }
                                    })()
                                }
                                {(() => {
                                    if (this.props.canTeacherPaike) {
                                        return <Button type="primary" className="xietongButton" onClick={() => { this.props.handleXietong(this.props.canTeacherPaike) }}>协同已开启</Button>
                                    } else {
                                        return <Button type="default" className="xietongButton2" onClick={() => { this.props.handleXietong(this.props.canTeacherPaike) }}>协同未开启</Button>
                                    }
                                })()}

                            </div>
                        }
                    })()}


                </HomeTopWrapper>
                {/* 此处是HomeButtomWrapper*/}
                {/* 只有在选择了两个选项之后才会渲染表格区域 */}
                {(() => {
                    if (this.props.shijianSelectOptionOne === "" || this.props.shijianSelectOptionTwo === "") {
                        return <HomeButtomWrapper>
                            <ShijianDefault />
                            <Link to="/home/tiqian"><PreStep className="xuesheng">上一步</PreStep></Link>
                            <StepLine className="xuesheng" />
                            <Link to="/home/paike"><NextStep className="xuesheng">前往排课</NextStep></Link>
                        </HomeButtomWrapper>
                    } else {
                        const columns = this.props.shijianColumn.map((col) => {
                            return {
                                ...col,
                                align: "center",
                                render: (text, record, index) => {
                                    let color = "purple";
                                    if (text[1] === "1" && text !== "第1节" && text !== "第10节" && text !== "第11节" && text !== "第12节" && text !== "第13节" && text !== "第14节" && text !== "第15节" && text !== "第16节" && text !== "第17节" && text !== "第18节" && text !== "第19节") {
                                        return <Tag.CheckableTag checked={false} onChange={() => { this.handleCellClick(index, text[0], text[1]) }} color={color} style={{ fontSize: '14px', height: '100%', opacity: 0 }}>不排课</Tag.CheckableTag>
                                    } else if (text[1] === "0") {
                                        color = "geekblue"
                                        return <Tag.CheckableTag checked={true} onChange={() => { this.handleCellClick(index, text[0], text[1]) }} className="mytag" style={{ height: '100%', fontSize: '14px' }}>不排课</Tag.CheckableTag>
                                    } else {
                                        return <span style={{ fontWeight: 450, color: 'rgba(0, 0, 0, 0.85)' }}>{text}</span>
                                    }
                                },
                            }
                        })

                        //判定需要渲染哪张表
                        let timetable
                        if (this.props.shijianSelectOptionOne === "科目不排课时间") {
                            if (this.props.shijianSelectOptionTwo === "物理") { timetable = this.props.wuliTimetable }
                            if (this.props.shijianSelectOptionTwo === "化学") { timetable = this.props.huaxueTimetable }
                            if (this.props.shijianSelectOptionTwo === "生物") { timetable = this.props.shengwuTimetable }
                            if (this.props.shijianSelectOptionTwo === "历史") { timetable = this.props.lishiTimetable }
                            if (this.props.shijianSelectOptionTwo === "地理") { timetable = this.props.diliTimetable }
                            if (this.props.shijianSelectOptionTwo === "政治") { timetable = this.props.zhengzhiTimetable }
                        }
                        if (this.props.shijianSelectOptionOne === "教师不排课时间") {
                            for (var i = 0; i < this.props.teacherTimetable.length; i++) {
                                if (this.props.teacherTimetable[i]['id'].toString() === this.props.shijianSelectOptionTwo) {
                                    timetable = this.props.teacherTimetable[i]['timetable']
                                }
                            }

                        }

                        return <HomeButtomWrapper className="hasItem">
                            <Table
                                rowKey={(r, i) => (i)}
                                className="timeTable"
                                dataSource={timetable}
                                columns={columns}
                                bordered={true}
                                pagination={{
                                    simple: true,
                                    position: 'top',
                                    showQuickJumper: true,
                                    hideOnSinglePage: true,
                                    total: this.props.wuliTimetable.length,
                                    hideOnSinglePage: false,
                                    defaultPageSize: 10,
                                    onChange: (page, pageSize) => {
                                        this.props.handleTimetablePage(page)
                                    }
                                }}
                            >
                            </Table>

                            {/* 排课提示 根据用户权限进行隐藏/显示 */}
                            {(() => {
                                if (this.props.userName.indexOf("teacher") !== -1) {
                                    return null
                                } else {
                                    return <TipsHint className="tips">Tips：默认为允许排课，点击对应的格子，设置为不排课</TipsHint>
                                }
                            })()}

                            {/* 底部操作按钮 根据用户权限进行变化 */}
                            {(() => {
                                if (this.props.userName.indexOf("teacher") !== -1) {
                                    return <div>
                                        <Link to="/home/tiqian"><PreStep className="xuesheng tongzhibuttom">返回首页</PreStep></Link>
                                        <StepLine className="xuesheng hasItemLine" />
                                    </div>
                                } else {
                                    return <div>
                                        <Link to="/home/tiqian"><PreStep className="xuesheng hasItemStep">上一步</PreStep></Link>
                                        <StepLine className="xuesheng hasItemLine" />
                                        <Link to="/home/paike"><NextStep className="xuesheng hasItemStep">前往排课</NextStep></Link>
                                    </div>
                                }
                            })()}

                        </HomeButtomWrapper>
                    }
                })()}
            </div>
        }
    }



    handleCellClick(hang, lie, value) {
        let futherValue
        if (value === "1") futherValue = "0"
        if (value === "0") futherValue = "1"
        if (this.props.timetablePage.toString() === "2") {
            hang = hang + 10
        }
        let temp = []
        let teacherTemp
        if (this.props.shijianSelectOptionOne === "科目不排课时间") {
            if (this.props.shijianSelectOptionTwo === "物理") {
                temp = this.props.wuliTimetable
                let realLie = "day" + lie
                temp[hang][realLie] = lie + futherValue
                this.props.changeSubjectTimetable(temp, this.props.shijianSelectOptionTwo)
            }
            if (this.props.shijianSelectOptionTwo === "化学") {
                temp = this.props.huaxueTimetable
                let realLie = "day" + lie
                temp[hang][realLie] = lie + futherValue
                this.props.changeSubjectTimetable(temp, this.props.shijianSelectOptionTwo)
            }
            if (this.props.shijianSelectOptionTwo === "生物") {
                temp = this.props.shengwuTimetable
                let realLie = "day" + lie
                temp[hang][realLie] = lie + futherValue
                this.props.changeSubjectTimetable(temp, this.props.shijianSelectOptionTwo)
            }
            if (this.props.shijianSelectOptionTwo === "历史") {
                temp = this.props.lishiTimetable
                let realLie = "day" + lie
                temp[hang][realLie] = lie + futherValue
                this.props.changeSubjectTimetable(temp, this.props.shijianSelectOptionTwo)
            }
            if (this.props.shijianSelectOptionTwo === "地理") {
                temp = this.props.diliTimetable
                let realLie = "day" + lie
                temp[hang][realLie] = lie + futherValue
                this.props.changeSubjectTimetable(temp, this.props.shijianSelectOptionTwo)
            }
            if (this.props.shijianSelectOptionTwo === "政治") {
                temp = this.props.zhengzhiTimetable
                let realLie = "day" + lie
                temp[hang][realLie] = lie + futherValue
                this.props.changeSubjectTimetable(temp, this.props.shijianSelectOptionTwo)
            }
        }
        if (this.props.shijianSelectOptionOne === "教师不排课时间") {
            teacherTemp = this.props.teacherTimetable
            for (var i = 0; i < teacherTemp.length; i++) {
                if (teacherTemp[i]['id'].toString() === this.props.shijianSelectOptionTwo) {
                    let realLie = "day" + lie
                    teacherTemp[i]['timetable'][hang][realLie] = lie + futherValue
                    this.props.changeTeacherTimetable(teacherTemp)
                }
            }

        }
    }

}

const mapStateToProps = (state) => {
    return {
        // 其他页面关联数据
        teacherData: state.getIn(['home', 'teacherData']).toJS(),
        day: state.getIn(['home', 'day']),
        course: state.getIn(['home', 'course']),

        //下拉菜单数据
        shijianClassifyData: state.getIn(['home', 'shijianClassifyData']).toJS(),
        shijianSubjectData: state.getIn(['home', 'shijianSubjectData']).toJS(),
        shijianTeacherData: state.getIn(['home', 'shijianTeacherData']).toJS(),

        //选中项数据
        shijianSelectOptionOne: state.getIn(['home', 'shijianSelectOptionOne']),
        shijianSelectOptionTwo: state.getIn(['home', 'shijianSelectOptionTwo']),

        //科目和老师的Timetable
        wuliTimetable: state.getIn(['home', 'wuliTimetable']).toJS(),
        huaxueTimetable: state.getIn(['home', 'huaxueTimetable']).toJS(),
        shengwuTimetable: state.getIn(['home', 'shengwuTimetable']).toJS(),
        lishiTimetable: state.getIn(['home', 'lishiTimetable']).toJS(),
        diliTimetable: state.getIn(['home', 'diliTimetable']).toJS(),
        zhengzhiTimetable: state.getIn(['home', 'zhengzhiTimetable']).toJS(),
        teacherTimetable: state.getIn(['home', 'teacherTimetable']).toJS(),

        //表格标题（第一行）
        shijianColumn: state.getIn(['home', 'shijianColumn']).toJS(),

        //当前页码，用于判断是否需要 index+10
        timetablePage: state.getIn(['home', 'timetablePage']),

        //协同排课开关
        canTeacherPaike: state.getIn(['home', 'canTeacherPaike']),

        //用户权限判定
        userName: state.get('header').get('userName'),
    }
}




const mapDispatchToProps = (dispatch) => {
    return {
        setTeacherData(data) {
            dispatch(actionCreators.handleTeacherFileUploadAction(data, true))
        },
        setJieciDay(value) {
            dispatch(actionCreators.handleDayChangeAction(value, true, "", ""))
        },
        setJieciCourse(value) {
            dispatch(actionCreators.handleCourseChangeAction(value, true, "", ""))
        },
        setShijianTeacherData(tempData) {
            dispatch(actionCreators.changeShijianTeacherDataAction(tempData))
        },
        setShijianSelectOptionOne(value) {
            dispatch(actionCreators.handleOptionOneChangeAction(value))
        },
        setShijianColumn(value) {
            dispatch(actionCreators.setShijianColumnAction(value))
        },

        handleOptionOneChange(value, teacherData) {
            console.log(`111selected ${value}`)
            if (value === "教师不排课时间") {
                let tempData = []
                for (var i = 0; i < teacherData.length; i++) {
                    let tempArray = [{
                        id: teacherData[i]['id1'],
                        name: teacherData[i]['name1'],
                    }]
                    tempData = tempData.concat(tempArray)
                }
                dispatch(actionCreators.changeShijianTeacherDataAction(tempData))
            }
            dispatch(actionCreators.handleOptionOneChangeAction(value))
        },
        handleOptionTwoChange(value) {
            // console.log(`selected ${value}`);// 输出的是 teacher id
            dispatch(actionCreators.handleOptionTwoChangeAction(value))
        },
        changeShijianTeacherData(tempData) {
            // console.log(tempData)
            dispatch(actionCreators.changeShijianTeacherDataAction(tempData))
        },
        changeSubjectTimetable(tempData, OptionTwo) {
            dispatch(actionCreators.changeSubjectTimetableAction(tempData, OptionTwo))
        },
        changeTeacherTimetable(tempData) {
            // console.log(tempData)
            dispatch(actionCreators.changeTeacherTimetableAction(tempData))
        },
        handleTimetablePage(page) {
            // console.log(page, "timetable page")
            dispatch(actionCreators.handleTimetablePageAction(page))
        },

        handleXietong(value) {
            dispatch(actionCreators.handleXietongAction(!value))
        },
        setXietong(value) {
            dispatch(actionCreators.handleXietongAction(value))
        }
    }
}





export default connect(mapStateToProps, mapDispatchToProps)(Shijian)
