import React from 'react'
import { connect } from 'react-redux'
import { actionCreators } from '../store'
import { Link } from 'react-router-dom'
import {
    HomeMainWrapper, TitleLine, TitleText, NextStep, StepLine, InputWrapper, InputTitle, Input
} from '../style';
import { Popover } from 'antd';

class Jieci extends React.Component {

    constructor(props) {
        super(props)
        window.storage.load({
            key: 'teacherData',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setTeacherData(ret.teacherData)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciDay',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciDay(ret.day)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciCourse',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciCourse(ret.course)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciClassroom',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciClassroom(ret.classroom)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciCapacity',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciCapacity(ret.capacity)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciClass',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciClass(ret.class)
        }).catch(err => {
            console.log(err)
        });
        window.storage.load({
            key: 'jieciClassnumber',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setJieciClassnumber(ret.classnumber)
        }).catch(err => {
            console.log(err)
        });

    }

    render() {
        let hasDayItem, hasCourseItem, hasClassroomItem, hasCapacityItem, hasClassItem, hasClassnumberItem
        if (this.props.day === "") {
            hasDayItem = ""
        } else {
            hasDayItem = "hasItem"
        }
        if (this.props.course === "") {
            hasCourseItem = ""
        } else {
            hasCourseItem = "hasItem"
        }
        if (this.props.classroom === "") {
            hasClassroomItem = ""
        } else {
            hasClassroomItem = "hasItem"
        }
        if (this.props.capacity === "") {
            hasCapacityItem = ""
        } else {
            hasCapacityItem = "hasItem"
        }
        if (this.props.class === "") {
            hasClassItem = ""
        } else {
            hasClassItem = "hasItem"
        }
        if (this.props.classnumber === "") {
            hasClassnumberItem = ""
        } else {
            hasClassnumberItem = "hasItem"
        }

        return <div>
            <HomeMainWrapper>
                <TitleLine />
                <TitleText>基础信息 - 节次设置</TitleText>
                <InputWrapper className="day">
                    <InputTitle>每周上课天数</InputTitle>
                    <Input value={this.props.day} onChange={(e) => { this.props.handleDayChange(e, this.props.course, this.props.teacherData) }} placeholder='请输入每周上课天数' className={hasDayItem}></Input>
                </InputWrapper>
                <InputWrapper className="course">
                    <InputTitle>每天上课节数</InputTitle>
                    <Input value={this.props.course} onChange={(e) => { this.props.handleCourseChange(e, this.props.day, this.props.teacherData) }} placeholder='请输入每天上课节数' className={hasCourseItem}></Input>
                </InputWrapper>
                <InputWrapper className="classroom">
                    <InputTitle>现有教室数量</InputTitle>
                    <Input value={this.props.classroom} onChange={this.props.handleClassroomChange} placeholder='请输入现有教室数量' className={hasClassroomItem}></Input>
                </InputWrapper>
                <Popover mouseEnterDelay={1.5} content="单个教室所能容纳的人数" placement="bottomLeft" overlayStyle={{ fontSize: 14 }}>
                    <InputWrapper className="capacity">
                        <InputTitle>教室容纳人数</InputTitle>
                        <Input value={this.props.capacity} onChange={this.props.handleCapacityChange} placeholder='请输入教室容纳人数' className={hasCapacityItem}></Input>
                    </InputWrapper>
                </Popover>
                <InputWrapper className="class">
                    <InputTitle>行政班级数量</InputTitle>
                    <Input value={this.props.class} onChange={this.props.handleClassChange} placeholder='请输入行政班级数量' className={hasClassItem}></Input>
                </InputWrapper>
                <Popover content="为每个班级设置一个最小的人数" placement="bottomLeft" overlayStyle={{ fontSize: 14 }}>
                    <InputWrapper className="classnumber">
                        <InputTitle>班级最小人数</InputTitle>
                        <Input value={this.props.classnumber} onChange={this.props.handleClassnumberChange} placeholder='请输入班级最小人数' className={hasClassnumberItem}></Input>
                    </InputWrapper>
                </Popover>
                <StepLine className="jieci" />
                <Link to="/home/xuesheng"><NextStep className="jieci">下一步</NextStep></Link>
            </HomeMainWrapper>
        </div>
    }

}


const mapStateToProps = (state) => {
    return {
        day: state.getIn(['home', 'day']),
        course: state.getIn(['home', 'course']),
        classroom: state.getIn(['home', 'classroom']),
        capacity: state.getIn(['home', 'capacity']),
        class: state.getIn(['home', 'class']),
        classnumber: state.getIn(['home', 'classnumber']),
        teacherData: state.getIn(['home', 'teacherData']).toJS(),
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleDayChange(e, course, teacherData) {
            dispatch(actionCreators.handleDayChangeAction(e.target.value, false, course, teacherData))
        },
        handleCourseChange(e, day, teacherData) {
            dispatch(actionCreators.handleCourseChangeAction(e.target.value, false, day, teacherData))
        },
        handleClassroomChange(e) {
            dispatch(actionCreators.handleClassroomChangeAction(e.target.value))
        },
        handleCapacityChange(e) {
            dispatch(actionCreators.handleCapacityChangeAction(e.target.value))
        },
        handleClassChange(e) {
            dispatch(actionCreators.handleClassChangeAction(e.target.value))
        },
        handleClassnumberChange(e) {
            dispatch(actionCreators.handleClassnumberChangeAction(e.target.value))
        },
        setJieciDay(value) {
            dispatch(actionCreators.handleDayChangeAction(value, true, "", ""))
        },
        setJieciCourse(value) {
            dispatch(actionCreators.handleCourseChangeAction(value, true, "", ""))
        },
        setJieciClassroom(value) {
            dispatch(actionCreators.handleClassroomChangeAction(value))
        },
        setJieciCapacity(value) {
            dispatch(actionCreators.handleCapacityChangeAction(value))
        },
        setJieciClass(value) {
            dispatch(actionCreators.handleClassChangeAction(value))
        },
        setJieciClassnumber(value) {
            dispatch(actionCreators.handleClassnumberChangeAction(value))
        },
        setTeacherData(data) {
            dispatch(actionCreators.handleTeacherFileUploadAction(data, true))
        }

    }
}



export default connect(mapStateToProps, mapDispatchToProps)(Jieci)
