import { fromJS } from 'immutable'
import * as constants from './constants'
import React from 'react'
import { Tag } from 'antd';


const defaultState = fromJS({
    day: "",
    course: "",
    classroom: "",
    capacity: "",
    class: "",
    classnumber: "",

    studentData: [],
    teacherData: [],

    studentColumn: [{
        title: "学号",
        dataIndex: "id",
        key: "id",
    }, {
        title: "姓名",
        dataIndex: "name",
        key: "name",
    }, {
        title: "班级",
        dataIndex: "class",
        key: "class",
    }, {
        title: "科目一",
        dataIndex: "subject1",
        key: "subject1",
    }, {
        title: "科目二",
        dataIndex: "subject2",
        key: "subject2",
    }, {
        title: "科目三",
        dataIndex: "subject3",
        key: "subject3",
    }, {
        title: "总分",
        dataIndex: "score",
        key: "score",
    }],

    tiqianStudentColumn: [{
        title: "预分班级",
        dataIndex: "class",
        key: "class",
        render: text => {
            let color = "purple";
            if (text === "暂无") color = ""
            return <Tag color={color} style={{ fontSize: '14px' }}>{text}</Tag>
        },
        width: 120,
        align: "center",
        sorter: (a, b) => a.class.charCodeAt(0) - b.class.charCodeAt(0),
        sortDirections: ['descend', 'ascend'],
    }, {
        title: "学号",
        dataIndex: "id",
        key: "id",
        sorter: (a, b) => a.id - b.id,
        sortDirections: ['descend', 'ascend'],
    }, {
        title: "姓名",
        dataIndex: "name",
        key: "name",
    }, {
        title: "科目一",
        dataIndex: "subject1",
        key: "subject1",
    }, {
        title: "科目二",
        dataIndex: "subject2",
        key: "subject2",
    }, {
        title: "科目三",
        dataIndex: "subject3",
        key: "subject3",
    }, {
        title: "总分",
        dataIndex: "score",
        key: "score",
        sorter: (a, b) => a.score - b.score,
        sortDirections: ['descend', 'ascend'],
    }],

    teacherColumn: [{
        title: "教工号",
        dataIndex: "id1",
        key: "id1",
    }, {
        title: "姓名",
        dataIndex: "name1",
        key: "name1",
    }, {
        title: "所授科目",
        dataIndex: "subject1",
        key: "subject1",
    }, {
        title: "教工号",
        dataIndex: "id2",
        key: "id2",
    }, {
        title: "姓名",
        dataIndex: "name2",
        key: "name2",
    }, {
        title: "所授科目",
        dataIndex: "subject2",
        key: "subject2",
    }],

    tiqianStudentData: [],
    selectWuli: true,
    selectHuaxue: true,
    selectShengwu: true,
    selectLishi: true,
    selectDili: true,
    selectZhengzhi: true,
    selectedRows: [],
    selectedRowKeys: [],

    //【时间安排】数据
    shijianClassifyData: ['科目不排课时间', '教师不排课时间'],
    shijianSubjectData: ['物理', '化学', '生物', '历史', '地理', '政治'],
    shijianTeacherData: [],
    shijianSelectOptionOne: "",
    shijianSelectOptionTwo: "",

    shijianColumn: [],

    wuliTimetable: [],
    huaxueTimetable: [],
    shengwuTimetable: [],
    lishiTimetable: [],
    diliTimetable: [],
    zhengzhiTimetable: [],
    teacherTimetable: [],

    timetablePage: "",

    paikeState: 1,
    currentPercent: 0,

    randomSchedule: [],
    randomScheduleId: "",

    canTeacherPaike: false,

    randomTeacherTimetable: [],

    helpAnchor: 0,
})

export default (state = defaultState, action) => {

    switch (action.type) {
        case constants.CHANGE_DAY_VALUE:
            // 当上课天数发生变化时，更新时间安排里的表格第一行
            let tempData = []
            if (action.value !== "") {
                for (var i = 0; i <= parseInt(action.value); i++) {
                    let tempArray = []
                    if (i === 0) {
                        tempArray = [{
                            title: "",
                            dataIndex: "jieshu",
                            key: "jieshu",
                            width: 160,
                            align: "center",
                            className: "colbackground"
                        }]
                    }
                    if (i === 1) {
                        tempArray = [{
                            title: "星期一",
                            dataIndex: "day1",
                            key: "day1",
                        }]
                    }
                    if (i === 2) {
                        tempArray = [{
                            title: "星期二",
                            dataIndex: "day2",
                            key: "day2",
                        }]
                    }
                    if (i === 3) {
                        tempArray = [{
                            title: "星期三",
                            dataIndex: "day3",
                            key: "day3",
                        }]
                    }
                    if (i === 4) {
                        tempArray = [{
                            title: "星期四",
                            dataIndex: "day4",
                            key: "day4",
                        }]
                    }
                    if (i === 5) {
                        tempArray = [{
                            title: "星期五",
                            dataIndex: "day5",
                            key: "day5",
                        }]
                    }
                    if (i === 6) {
                        tempArray = [{
                            title: "星期六",
                            dataIndex: "day6",
                            key: "day6",
                        }]
                    }
                    if (i === 7) {
                        tempArray = [{
                            title: "星期日",
                            dataIndex: "day7",
                            key: "day7",
                        }]
                    }
                    tempData = tempData.concat(tempArray)
                }
            }

            if (action.key) {
                console.log("jieciDay no delete")
                window.storage.save({
                    key: 'jieciDay',
                    data: {
                        day: action.value
                    }
                })
                window.storage.save({
                    key: 'shijianColumn',
                    data: {
                        shijianColumn: fromJS(tempData)
                    }
                })
                return state.merge({
                    'day': action.value,
                    'shijianColumn': fromJS(tempData)
                })
            } else {
                console.log("jieciDay delete")
                window.storage.save({
                    key: 'jieciDay',
                    data: {
                        day: action.value
                    }
                })
                // remove 时间安排里的各项数据
                // window.storage.remove({
                //     key: 'tiqianStudentData'
                // })
                window.storage.save({
                    key: 'wuliTimetable',
                    data: {
                        wuliTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'huaxueTimetable',
                    data: {
                        huaxueTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'shengwuTimetable',
                    data: {
                        shengwuTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'lishiTimetable',
                    data: {
                        lishiTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'diliTimetable',
                    data: {
                        diliTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'zhengzhiTimetable',
                    data: {
                        zhengzhiTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'teacherTimetable',
                    data: {
                        teacherTimetable: fromJS(action.teacherTimetable)
                    }
                })
                window.storage.save({
                    key: 'shijianColumn',
                    data: {
                        shijianColumn: fromJS(tempData)
                    }
                })

                return state.merge({
                    'day': action.value,
                    'shijianColumn': fromJS(tempData),
                    'wuliTimetable': fromJS(action.subjectData),
                    'huaxueTimetable': fromJS(action.subjectData),
                    'shengwuTimetable': fromJS(action.subjectData),
                    'lishiTimetable': fromJS(action.subjectData),
                    'diliTimetable': fromJS(action.subjectData),
                    'zhengzhiTimetable': fromJS(action.subjectData),
                    'teacherTimetable': fromJS(action.teacherTimetable),
                })
            }
        case constants.CHANGE_COURSE_VALUE:
            if (action.key) {
                console.log("jieciCourse no delete")
                window.storage.save({
                    key: 'jieciCourse',
                    data: {
                        course: action.value
                    }
                })
                return state.merge({
                    'course': action.value
                })
            } else {
                console.log("jieciCourse delete")
                window.storage.save({
                    key: 'jieciCourse',
                    data: {
                        course: action.value
                    }
                })
                // remove 时间安排里的各项数据
                // window.storage.remove({
                //     key: 'tiqianStudentData'
                // })
                window.storage.save({
                    key: 'wuliTimetable',
                    data: {
                        wuliTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'huaxueTimetable',
                    data: {
                        huaxueTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'shengwuTimetable',
                    data: {
                        shengwuTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'lishiTimetable',
                    data: {
                        lishiTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'diliTimetable',
                    data: {
                        diliTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'zhengzhiTimetable',
                    data: {
                        zhengzhiTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'teacherTimetable',
                    data: {
                        teacherTimetable: fromJS(action.teacherTimetable)
                    }
                })

                return state.merge({
                    'course': action.value,
                    'wuliTimetable': fromJS(action.subjectData),
                    'huaxueTimetable': fromJS(action.subjectData),
                    'shengwuTimetable': fromJS(action.subjectData),
                    'lishiTimetable': fromJS(action.subjectData),
                    'diliTimetable': fromJS(action.subjectData),
                    'zhengzhiTimetable': fromJS(action.subjectData),
                    'teacherTimetable': fromJS(action.teacherTimetable),
                })
            }
        case constants.CHANGE_CLASSROOM_VALUE:
            window.storage.save({
                key: 'jieciClassroom',
                data: {
                    classroom: action.value
                }
            })
            return state.set('classroom', action.value)
        case constants.CHANGE_CAPACITY_VALUE:
            window.storage.save({
                key: 'jieciCapacity',
                data: {
                    capacity: action.value
                }
            })
            return state.set('capacity', action.value)
        case constants.CHANGE_CLASS_VALUE:
            window.storage.save({
                key: 'jieciClass',
                data: {
                    class: action.value
                }
            })
            return state.set('class', action.value)
        case constants.CHANGE_CLASSNUMBER_VALUE:
            window.storage.save({
                key: 'jieciClassnumber',
                data: {
                    classnumber: action.value
                }
            })
            return state.set('classnumber', action.value)
        case constants.CHANGE_STUDENT_DATA:
            if (action.key) {
                console.log("no delete")
                window.storage.save({
                    key: 'studentData',
                    data: {
                        studentData: action.value
                    }
                })
                return state.merge({
                    'studentData': action.value
                })
            } else {
                console.log("delete")
                window.storage.save({
                    key: 'studentData',
                    data: {
                        studentData: action.value
                    }
                })
                window.storage.remove({
                    key: 'tiqianStudentData'
                })
                return state.merge({
                    'studentData': action.value
                })
            }
        case constants.CHANGE_TEACHER_DATA:
            if (action.key) {
                console.log("teacher no delete")
                window.storage.save({
                    key: 'teacherData',
                    data: {
                        teacherData: action.value
                    }
                })
                return state.merge({
                    'teacherData': action.value
                })
            } else {
                console.log("teacher delete")
                window.storage.save({
                    key: 'teacherData',
                    data: {
                        teacherData: action.value
                    }
                })

                // remove 时间安排里的各项数据
                // window.storage.remove({
                //     key: 'tiqianStudentData'
                // })
                window.storage.save({
                    key: 'wuliTimetable',
                    data: {
                        wuliTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'huaxueTimetable',
                    data: {
                        huaxueTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'shengwuTimetable',
                    data: {
                        shengwuTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'lishiTimetable',
                    data: {
                        lishiTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'diliTimetable',
                    data: {
                        diliTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'zhengzhiTimetable',
                    data: {
                        zhengzhiTimetable: fromJS(action.subjectData)
                    }
                })
                window.storage.save({
                    key: 'teacherTimetable',
                    data: {
                        teacherTimetable: fromJS(action.teacherTimetable)
                    }
                })

                return state.merge({
                    'teacherData': action.value,
                    'wuliTimetable': fromJS(action.subjectData),
                    'huaxueTimetable': fromJS(action.subjectData),
                    'shengwuTimetable': fromJS(action.subjectData),
                    'lishiTimetable': fromJS(action.subjectData),
                    'diliTimetable': fromJS(action.subjectData),
                    'zhengzhiTimetable': fromJS(action.subjectData),
                    'teacherTimetable': fromJS(action.teacherTimetable),
                })
            }
        case constants.CHANGE_TIQIAN_STUDENT_DATA:
            console.log("qitian save")
            window.storage.save({
                key: 'tiqianStudentData',
                data: {
                    tiqianStudentData: action.value
                }
            })
            return state.merge({
                'selectedRows': fromJS([]),
                'selectedRowKeys': fromJS([]),
                'tiqianStudentData': action.value
            })
        case constants.CLICK_FILTER:
            if (action.id === 1) {
                return state.set('selectWuli', action.value)
            }
            if (action.id === 2) {
                return state.set('selectHuaxue', action.value)
            }
            if (action.id === 3) {
                return state.set('selectShengwu', action.value)
            }
            if (action.id === 4) {
                return state.set('selectLishi', action.value)
            }
            if (action.id === 5) {
                return state.set('selectDili', action.value)
            }
            if (action.id === 6) {
                return state.set('selectZhengzhi', action.value)
            }
        case constants.RECORD_SELECTED_ROWS:
            return state.merge({
                'selectedRows': action.value,
                'selectedRowKeys': action.key
            })
        case constants.CHANGE_OPTION_ONE:
            window.storage.save({
                key: 'shijianSelectOptionOne',
                data: {
                    shijianSelectOptionOne: action.value
                }
            })
            window.storage.save({
                key: 'shijianSelectOptionTwo',
                data: {
                    shijianSelectOptionTwo: ""
                }
            })
            return state.merge({
                'shijianSelectOptionOne': action.value,
                'shijianSelectOptionTwo': ""
            })
        case constants.CHANGE_OPTION_TWO:
            window.storage.save({
                key: 'shijianSelectOptionTwo',
                data: {
                    shijianSelectOptionTwo: action.value
                }
            })
            return state.merge({
                'shijianSelectOptionTwo': action.value
            })
        case constants.CHANGE_SHIJIAN_TEACHER_DATA:
            window.storage.save({
                key: 'shijianTeacherData',
                data: {
                    shijianTeacherData: action.value
                }
            })
            return state.merge({
                'shijianTeacherData': action.value
            })
        case constants.CHANGE_SHIJIAN_COLUMN:
            window.storage.save({
                key: 'shijianColumn',
                data: {
                    shijianColumn: action.value
                }
            })
            return state.merge({
                'shijianColumn': action.value
            })
        // case constants.RESET_SUBJECT_TIMETABLE:
        //     return state.merge({
        //         'wuliTimetable': fromJS(action.value),
        //         'huaxueTimetable': fromJS(action.value),
        //         'shengwuTimetable': fromJS(action.value),
        //         'lishiTimetable': fromJS(action.value),
        //         'diliTimetable': fromJS(action.value),
        //         'zhengzhiTimetable': fromJS(action.value),
        //     })
        case constants.CHANGE_WULI_TIMETABLE:
            window.storage.save({
                key: 'wuliTimetable',
                data: {
                    wuliTimetable: action.value
                }
            })
            return state.merge({
                'wuliTimetable': action.value
            })
        case constants.CHANGE_HUAXUE_TIMETABLE:
            window.storage.save({
                key: 'huaxueTimetable',
                data: {
                    huaxueTimetable: action.value
                }
            })
            return state.merge({
                'huaxueTimetable': action.value
            })
        case constants.CHANGE_SHENGWU_TIMETABLE:
            window.storage.save({
                key: 'shengwuTimetable',
                data: {
                    shengwuTimetable: action.value
                }
            })
            return state.merge({
                'shengwuTimetable': action.value
            })
        case constants.CHANGE_LISHI_TIMETABLE:
            window.storage.save({
                key: 'lishiTimetable',
                data: {
                    lishiTimetable: action.value
                }
            })
            return state.merge({
                'lishiTimetable': action.value
            })
        case constants.CHANGE_DILI_TIMETABLE:
            window.storage.save({
                key: 'diliTimetable',
                data: {
                    diliTimetable: action.value
                }
            })
            return state.merge({
                'diliTimetable': action.value
            })
        case constants.CHANGE_ZHENGZHI_TIMETABLE:
            window.storage.save({
                key: 'zhengzhiTimetable',
                data: {
                    zhengzhiTimetable: action.value
                }
            })
            return state.merge({
                'zhengzhiTimetable': action.value
            })
        case constants.CHANGE_TEACHER_TIMETABLE:
            window.storage.save({
                key: 'teacherTimetable',
                data: {
                    teacherTimetable: action.value
                }
            })
            return state.merge({
                'teacherTimetable': action.value
            })
        case constants.CHANGE_TIMETABLE_PAGE:
            return state.merge({
                'timetablePage': action.value
            })
        case constants.SET_SHIJIAN_COLUMN:
            window.storage.save({
                key: 'shijianColumn',
                data: {
                    shijianColumn: fromJS(action.value)
                }
            })
            return state.merge({
                'shijianColumn': fromJS(action.value)
            })
        case constants.CHANGE_PAIKE_STATE:
            window.storage.save({
                key: 'paikeState',
                data: {
                    paikeState: action.value
                }
            })
            return state.merge({
                'paikeState': action.value
            })
        case constants.CHANGE_PERCENT:
            return state.merge({
                'currentPercent': action.value
            })
        case constants.HANDLE_RANDOM_SCHEDULE:
            window.storage.save({
                key: 'randomSchedule',
                data: {
                    randomSchedule: action.value
                }
            })
            return state.merge({
                'randomSchedule': fromJS(action.value)
            })
        case constants.HANDLE_SCHEDULE_ID:
            window.storage.save({
                key: 'randomScheduleId',
                data: {
                    randomScheduleId: action.value
                }
            })
            return state.merge({
                'randomScheduleId': action.value
            })
        case constants.HANDLE_XIETONG_FLAG:
            window.storage.save({
                key: 'canTeacherPaike',
                data: {
                    canTeacherPaike: action.value
                }
            })
            return state.merge({
                'canTeacherPaike': action.value
            })
        case constants.HANDLE_RANDOM_TEACHER_TIMETABLE:
            window.storage.save({
                key: 'randomTeacherTimetable',
                data: {
                    randomTeacherTimetable: action.value
                }
            })
            return state.merge({
                'randomTeacherTimetable': fromJS(action.value)
            })
        case constants.CHANGE_ANCHOR:
            return state.merge({
                'helpAnchor': action.value
            })
        default:
            return state
    }

}

