import React from 'react'
import { connect } from 'react-redux'
import { LoginWrapper, LogoWrapper, Input, LogoTitle, Logo, LoginBox, LoginTitle, BackgroundDecorate1, BackgroundDecorate2, LoginFooter, LoginMainTitle, LoginSubTitle, SignupText } from './style'
import { actionCreators } from './store'
import { Link, Redirect } from 'react-router-dom'
import { Checkbox, Button } from 'antd'


class Login extends React.Component {
    constructor(props) {
        super(props)
        window.storage.load({
            key: 'loginStatus',
            autoSync: true,
            syncInBackground: true
        }).then(ret => {
            this.props.setLoginStatus(ret.isLogin)

        }).catch(err => {
            console.log(err)
        });
    }

    render() {
        if (!this.props.loginStatus) {
            return (
                <LoginWrapper>
                    <BackgroundDecorate2 /><BackgroundDecorate1 />
                    <Link to="/login">
                        <LogoWrapper>
                            <Logo></Logo>
                            <LogoTitle>简道 智能排课系统</LogoTitle>
                        </LogoWrapper>
                    </Link>
                    <LoginSubTitle>INTELLIGENT COURSE SCHEDULE SYSTEM</LoginSubTitle>
                    <LoginMainTitle>
                        <div>欢迎使用</div>
                        <div>简道智能排课系统…</div>
                    </LoginMainTitle>

                    <LoginBox>
                        <LoginTitle>简道统一账号登录</LoginTitle>
                        <Input placeholder='请输入用户名' innerRef={(input) => { this.account = input }}></Input>
                        <Input placeholder='请输入密码' type='password' innerRef={(input) => { this.password = input }}></Input>
                        <Checkbox className="checkBox">下次自动登录</Checkbox>
                        <Button type="primary" className="button" onClick={() => this.props.login(this.account, this.password)}>登录</Button>
                        <Link to="/signup">
                            <SignupText>还没有注册账号？点我注册</SignupText>
                        </Link>
                    </LoginBox>
                    <LoginFooter>
                        <span>2019 &#169; made by  </span>
                        <span className="chucktzh">  Chucktzh</span>
                    </LoginFooter>
                </LoginWrapper>
            )
        } else {
            return <Redirect to='/instructor' />
        }

    }

}


const mapState = (state) => ({
    loginStatus: state.getIn(['login', 'login'])
})

const mapDispatch = (dispatch) => ({
    login(account, password) {
        dispatch(actionCreators.login(account.value, password.value))
        window.storage.save({
            key: 'userName',
            data: {
                name: account.value
            }
        })
    },

    setLoginStatus(isLogin) {
        dispatch(actionCreators.setLoginStatusAction(isLogin))
    }
})

export default connect(mapState, mapDispatch)(Login)